@media screen and (max-width: 375px) {
    header{       
        nav.navbar{
            background-color: #303030;
            .container-fluid{
                padding: 0;
                position: relative;
                height: 87px;
            }
            .navbar-brand{
                margin-right: 0;
                z-index: 1;
                img{
                    position: absolute;
                    top: 22px;
                    left: 0px;
                }
            }            
            .navbar-collapse{
                display: flex;
                flex-direction: column;
                position: relative;
                margin-left: 0px;
                height: 87px;
                .navbar-nav{
                    display: none;
                }
                .town{
                    position: absolute;
                    top: 13px;
                    left: 0px;
                    color: #7E7E7E;
                    margin-left: 196px;
                    width: 100px;
                    font-size: 10px;
                    line-height: 12px;
                }
                .menu-toggler {     
                    margin-top: 10px;
                    margin-left: 285px;           
                    position: relative;
                    height: 30px;
                    width: 50px;
                    top: 0;
                    right: 0;
                    z-index: 2;
                    .burger-name{
                        display: none;
                    }
                    &.active{
                        span{
                            width: 50px;
                            margin-left: 0px;
                            margin-top: 20px;
                            transform: rotate(45deg) translate(0px, 0px);
                        }
                    }
                    &.active{
                        span:nth-child(3) {
                            width: 50px;
                            margin-left:0px;
                            margin-top: 10px;
                            transform: rotate(-45deg) translate(14.5px, -13.5px);
                        }   
                    } 
                    &.active .burger-name{
                        display: none;
                    }                
                }
                .btn-primary{
                    display: none;  /* ,     first-screen!!!!!*/
                    position: absolute;
                    top: 491px;
                    left: 63px;
                    z-index: 1;
                    margin-left: 0;
                    padding: 22px 48px;
                    background: rgba(0, 0, 0, 0.5);
                    &:hover{
                        padding: 22px 46px 22px 45px;
                    }
                    &:focus{
                        padding: 22px 46px 22px 45px;
                    }
                } 
                .modal{
                    top: 40px;
                    height: 621px;
                    &-dialog{
                        max-width: 345px;
                        .modal-content{
                            padding: 60px 30px;
                            .modal-header{
                                margin-bottom: 45px;
                                .modal-title{
                                    font-size: 25px;                             
                                }
                            }                        
                            .modal-body{
                                &-description{
                                    font-size: 16px;
                                    margin-bottom: 40px;
                                }
                                &-form{     
                                    height: 220px;                               
                                    &-item{                                                                         
                                        input[type="text"], input[type="tel"]{
                                            max-width: 296px;
                                        }
                                        input[type="tel"]{
                                            margin-bottom: 105px;  
                                        }
                                    } 
                                }                    
                                textarea{
                                    max-width: 296px;                                    
                                }
                            }
                            .modal-footer{                    
                                .btn-send{
                                    margin-right: 15px;   
                                }
                            }                        
                        }
                    }                         
                }
                .modal#exampleModalToggleHeaderSent {
                    .modal-dialog{
                        max-width: 345px;
                        .modal-content{
                            padding: 60px 30px;
                            .modal-header{
                                .modal-title{
                                    font-size: 25px;                             
                                }
                            }                        
                            .modal-body{
                                &-description{
                                    p{
                                        font-size: 16px;
                                    }                                    
                                }                  
                            }
                            .modal-footer{                    
                                .btn{
                                    margin-right: 85px;   
                                }
                            }                      
                        }
                    }                     
                }
                .nav-menu{
                    display: none;
                    width: 100%;
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    background-color: rgba(48, 48, 48, 0.9);
                    z-index: 1;
                    &-list{ 
                        margin: 40px;                                            
                        .nav-menu-item{
                            padding: 0;
                            margin-bottom: 25px;
                            &::before{
                                content: "";
                                height: 0;
                            }
                            .nav-menu-link{
                                color: #FFFFFF;
                                font-size: 18px;
                                line-height: 22px; 
                                &:hover{
                                    text-decoration: none;
                                    opacity: .8;   
                                } 
                                &.active-menu{
                                    padding-bottom: 10px;
                                    box-shadow: inset 0 -.3px 0 0 $third-color;
                                }                            
                            }
                            &:nth-child(3), &:nth-child(6){
                                border-top: .3px solid rgba(255, 255, 255, 1);
                                padding-top: 30px;
                            }
                            &:nth-child(5){
                                margin-bottom: 30px;
                            }
                        }  
                    }
                    .more-list{
                        margin-top: 31px;
                        .more-item{
                            margin-bottom: 25px;
                            &::before{
                                content: "";
                                height: 0;                                
                            }
                            .more-link{
                                color: #fff;
                                font-size: 16px;
                                line-height: 20px;
                                &:hover{
                                    text-decoration: none;  
                                    opacity: .8; 
                                } 
                                &.active-more{
                                    color: $third-color;
                                    font-weight: 600;
                                } 
                                &.active-more{
                                    color: $third-color;
                                    font-weight: 600;
                                }
                            }
                        }
                    }
                }
            }        
        } 
    }
    .first-screen{
        .container{
            .scrollbottom{
                display: none;
            }
            .slider{    
                position: relative;                  
                &-item{
                    width: 100%; /*375px*/
                    height: 564px;
                    &-one, &-two, &-three{
                        position: relative;
                        &::after{
                            content: "";
                            width: 100%; /*375px*/
