                            background: url(assets/img/first-screen-after-mob.png) center no-repeat;
                            position: absolute;
                            top: 0px;
                            left: 0px;
                            z-index: -1;
                        }
                        &::before{
                            content: "";
                            width: 100%; /*375px*/
                            height: 564px;
                            background: rgba(0, 0, 0, 0.3);
                            position: absolute;
                            top: 0px;
                            left: 0px;
                            z-index: 0;
