                                position: absolute;
                                top: 240px;
                                left: 75px;
                            }
                        }                
                    }
                } 
                .slick-arrow {
                    &.slick-prev {
                        left:-60px;
                        top: 0px;
                    }            
                    &.slick-next {
                        right: -60px;
                        top: 0px;

                    }   
                }  
                .slides-numbers {
                    .active {
                        display: none;
                    }   
                    .total {
                        display: none;
                    }
                }  
            } 
        }     
        
