    .fwm-price-list{
        .container{
            flex-direction: column;
            position: relative;
            .price-list-img{
                display: none;
            }
            .price-list-info{
                padding: 60px 0;
                .price-list-title{
                    font-size: 25px;
                    line-height: 1;
                    margin-bottom: 50px;
                    margin-left: 35px;
                }
                .slider{
                    display: none;                    
                }                  
                .accordion{
                    display: block;
                    width: 100%;
                    .accordion-item{
                        border: none;
                        
                        .accordion-header{                            
                            .accordion-button{
                                font-family: $font-primary;
                                font-weight: 600;
                                line-height: 24px;
                                color: $third-color;
                                background: $primary-color;
                                padding: 20px 64px 20px 15px;    
                                &::after {
                                    content: "";
                                    background-image: none;
                                }
                                span{
                                    width: 16px;
                                    height: 2px;
                                    border-radius: 2px;
                                    display: block;
                                    position: absolute;
                                    top: 31px;
                                    right:23px;
                                    background-color: #3F3F3F;    
                                    transform: rotate(45deg) translate(0px, -2px);
                                    &:nth-child(2) {
                                        transform: rotate(135deg) translate(-9px, -7px);
                                    } 
                                }      
                                &:not(.collapsed) {
                                    background-color: $primary-color;    
                                    box-shadow: none;
                                    padding: 20px 64px 20px 15px;
                                    span {
                                        top: 31px;
                                        right:15px;
                                        background: #3F3F3F;
                                        transform: rotate(45deg) translate(0px, 1px);
                                        &:nth-child(2) {
                                            transform: rotate(-45deg) translate(-9px, -7px);
                                        } 
                                        z-index: 1;
                                    }                                                         
                                }
                                &.collapsed {
                                    border-bottom-right-radius: 0;
                                    border-bottom-left-radius:0;
                                }
                                &:focus{
                                    border: none;
                                    border-color: #fff;
                                    outline: 0;
                                    box-shadow: none;  
                                }                             
                            }
                        }
                        .accordion-body{
                            padding: 0 15px 30px;
                            background: $primary-color;
                            color: #fff;
                            .table-price{
                                color: #fff;
                                thead{
                                    font-size: 15px;
                                    line-height: 1;
                                    font-weight: 400;
                                    color: $third-color;
                                    tr{
                                        th{
                                           padding-bottom: 30px; 
                                           padding-right: 41px;
                                           &:last-child{
                                                padding-right: 0;
                                           }
                                        }
                                        
                                    }
                                }
                                tbody{
                                    font-size: 15px;
                                    line-height: 18px;
                                    font-weight: 300;
                                    tr{
                                        td{
                                            padding-bottom: 30px;
                                            &:nth-child(1){
                                                padding-right: 30px;
                                            }
                                            &:nth-child(2){
                                                padding-right: 24px;
                                            }
                    
                                        }
                                    }
                                }                    
                            }                            
                        }
                    }
                }
            }
            .price-list-img-mob{
                display: block;
                width: 375px;
                height: 433px;
            }
        }
       
    }
