                display: none;
            }
            .accordion{
                display: block;
                width: 100%;
                .accordion-item{
                    border: none;
                    .accordion-header{                            
                        .accordion-button{
                            font-size: 20px;
                            font-family: $font-primary;
                            font-weight: 600;
                            line-height: 24px;
                            color: $third-color;
                            background: rgba(23, 23, 23, 0.1);
                            padding: 20px 64px 20px 0;    
                            &::after {
                                content: "";
                                background-image: none;
                            }
                            span{
                                width: 16px;
                                height: 2px;
                                border-radius: 2px;
                                display: block;
                                position: absolute;
                                top: 31px;
                                right:23px;
                                background-color: #3F3F3F;    
                                transform: rotate(45deg) translate(0px, -2px);
                                &:nth-child(2) {
                                    transform: rotate(135deg) translate(-9px, -7px);
                                } 
                            }      
                            &:not(.collapsed) {
                                background-color: rgba(23, 23, 23, 0.1);    
                                box-shadow: none;
                                padding: 20px 64px 20px 0;
                                span {
                                    top: 31px;
                                    right:15px;
                                    background: #3F3F3F;
                                    transform: rotate(45deg) translate(0px, 1px);
                                    &:nth-child(2) {
                                        transform: rotate(-45deg) translate(-9px, -7px);
                                    } 
                                    z-index: 1;
                                }                                                         
                            }
                            &.collapsed {
                                border-bottom-right-radius: 0;
                                border-bottom-left-radius:0;
                            }
                            &:focus{
                                border: none;
                                border-color: #fff;
                                outline: 0;
