.fwm-price-list{
    .container{
        padding: 0;
        display: flex;
        background: $primary-color;
        .price-list-img{
            display: block;
        }
        .price-list-info{
            padding: 100px 100px 0 128px;
            .price-list-title{
                line-height: 37px;
                font-weight: 600;
                color: #fff;
                margin-bottom: 55px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(0deg);
                    position: absolute;
                    top: -20px;
                    left: -20px;                
                }
            }
            .slider{
                width: 610px;
                z-index: 2;
                position: relative;
                .slides-numbers {
                    color: #fff;
                    display: none;
                    position: absolute;
                    bottom:-20px;
                    left: 61px;
                    z-index: 3;
                    .active {
                        font-size: 20px;
                        line-height: 24px;
                        margin-right: 4px;
                        position: relative;
                        &::after{
                            content: "/";
                            position: absolute;
                            width: 8px;
                            top:0;
                            right: -9px;
                        }
                    }      
                    .total {
                        font-size: 15px;
                        line-height: 18px;
                    }
                }
                .slick-arrow {
                    width: 50px;
                    height: 10px;
                    background: url("assets/img/icon-arrow.png") center/contain no-repeat;
                    position: absolute;
                    z-index: 1;
                    &::before{
                        content: "";
                    }
                    &.slick-prev {
                        transform: rotate(180deg);
                        left: 0px;
                        top: 486px;
                    }            
                    &.slick-next {
                        left: 118px;
                        top:490px;
                    }
                    &:hover{
                        opacity: 0.7;
                    }        
                }  
                &-item{                
                    .table-price{
                        width: 610px;
                        height: 443px;
                        color: #fff;
                        thead{
                            font-size: 18px;
                            line-height: 22px;
                            font-weight: 600;
                            tr{
                                th{
                                   padding-bottom: 60px; 
                                   padding-right: 92px;
                                   &:nth-child(2){
                                        padding-right: 62px;
                                   }
                                   &:last-child{
                                        padding-right: 0;
                                   }
                                }
                                
                            }
                        }
                        tbody{
                            font-size: 15px;
                            line-height: 18px;
                            font-weight: 400;
                            tr{
                                td{
                                    padding-bottom: 30px;
                                    &:nth-child(2){
                                        padding-right: 91px;
                                    }
            
                                }
                            }
                        }                    
                    }   
                }
            }  
            .accordion{
                display: none;
            }      
        }
        .price-list-img-mob{
            display: none;
        }
    }   
}