.portfolio-best-work{  
    .container{
        padding: 0;
        background: $primary-color;
        .pbw-info{
            display: flex;
            .pbw-title{
                font-weight: 600;
                line-height: 37px;
                color: $white;        
                padding: 87px 195px 60px 120px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    position: absolute;
                    top: 67px;
                    left: 100px;                
                }
            }
            .pbw-nav{
                &-list{
                    display: flex;
                    margin-top: 92px;
                    .pbw-nav-item{
                        margin-right: 80px;
                        &::before{
                            content: "";
                            height: 0;
                        }
                        .pbw-nav-link{
                            color: $white;
                            font-size: 20px;
                            line-height: 24px;
                            font-family: $font-primary;
                            &:hover{
                                text-decoration: none;
                                font-family: $font-primary;
                            }
                            &.active-pbw{
                                font-weight: 700;
                                padding-bottom: 5px;
                                box-shadow: inset 0 -.3px 0 0 $third-color;
                                font-family: $font-primary;
                            }    
                        }
                    }
                }
            }
        }
        .hidden{
            display: none;
        }
        .pbw-apartments, .pbw-commercial{
            width: 100%;
            height: 900px;
            background: $secondary-color;
        }
        .pbw-cottages{
            width: 100%;
            height: 900px;
            .row{
                margin: 0;
            }
            .pbw-cottages-link{
                padding: 0;
                width: 360px;
                position: relative;
                .col-md-4{
                    padding: 0;
                    cursor: pointer;                    
                }
                &:hover::before{
                    content: "";
                    width: 360px;
                    height: 300px;
                    background: rgba(0, 0, 0, 0.5);
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    z-index: 1;
                }
                &:hover .pbw-cottages-name{
                    display: block;
                }
                .pbw-cottages-name{
                    display: none;
                    position: absolute;
                    top: 114px;
                    left: 87px;
                    color: $white;
                    font-size: 20px;
                    line-height: 24px;
                    font-weight: 600;
                    z-index: 1;
                }
            }
            
        }
        .pbw-more{
            display: flex;
            margin-left:421px;
            i{
                color: $white;
                font-weight: 300;
                font-size: 18px;
                line-height: 22px;
            }
            .pbw-quote{
                font-size: 18px;
                line-height: 22px;
                margin-top: 72px;
                margin-right: 65px;
            }
            .btn{
                width: 250px;
                margin-top: 50px;
                margin-bottom: 100px;
                text-decoration: none;
            }
        }
    }
    
}
