.rmm-what-can-do{
    .container{
        padding: 0;
        display: flex;
        background: $primary-color;
        .what-can-do-img{
            width: 710px;
            height: 863px;
        }
        .what-can-do-info{
            padding: 100px 144px 0 68px;
            .what-can-do-title{
                line-height: 37px;
                font-weight: 600;
                color: #fff;
                margin-bottom: 50px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    position: absolute;
                    top: -20px;
                    left: -20px;                
                }
            }
            .what-can-do-list{
                .what-can-do-item{
                    font-size: 18px;
                    font-weight: 300;
                    line-height: 20px;
                    color: #fff;
                    margin-bottom: 30px;
                    margin-left: 13px;
                    &:before {
                        content: "";
                        background-color: #fff;
                        border-radius: 50%;
                        width: 2px;
                        min-width: 2px;
                        height: 2px;
                        top: 7px;
                        left: -13px;
                    }
                }
            }
        }
    }
}