.fwm-other-types-work{
    .container{
        width: 1440px;
        height: 577px;
        background: url(assets/img/other-types-work-background.png) center no-repeat;
        padding: 79px 394px;
        position: relative;
        .other-types-work-form{
            display: flex;
            flex-direction: column;
            width: 652px;
            height: 419px;
            background: rgba(255, 255, 255, 0.7);
            &-title{
                font-weight: 600;
                padding: 81px 62px 50px 60px;
            }
            &-item{
                margin-left: 60px;
                input[type="text"], input[type="tel"]{
                    width: 358px;
                    background: 0;
                    border: 0;
                    color: $black;
                    font-family: $font-primary;
                    font-size: 15px;
                    line-height: 18px;
                    font-weight: 400;
                    margin-bottom: 30px;  
                    border-bottom: 1px solid #000;
                    padding: 0 0 10px 0;      
                    &:focus-visible{
                        outline: none;
                    }
                }
            }   
            
            .btn-warning{
                width: 250px;
                margin-left: 60px;
                padding: 19px 47px;
                &:hover {
                    width: 250px;
                }
                &:focus {
                    width: 250px;
                }
            }   
        }

        .card-block{
            position: absolute;  
            top: 98px;
            right: 394px;   
            width: 652px;
            height: 419px;
            background: #fff;
            padding: 60px;
            display: none;
            flex-direction: column;
            &-title{
                font-weight: 600;    
                margin-bottom: 40px;  
                padding-right: 50px;         
            }
            &-description{
                margin-bottom: 20px;
                p{
                    font-size: 18px;
                    line-height: 1;
                    margin-bottom: 30px;
                } 
            }
            .btn{
                width: 200px;
                margin-left: 0;
                margin-right: 335px;   
                padding: 19px 35px;
                &:hover{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
                &:focus{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
            }                  
        } 
    }
    
}