.dm-advice{
    .container{
        padding: 0;
        height: 577px;
        display: flex;
        position: relative;
        .dm-advice-form{
            display: flex;
            flex-direction: column;
            width: 652px;
            height: 419px;
            background: rgba(255, 255, 255, 0.7);
            margin: 100px 116px;
            &-title{
                font-weight: 600;
                line-height: 37px;
                padding: 60px 60px 50px;
            }
            &-item{
                margin-left: 60px;
                input[type="text"], input[type="tel"]{
                    width: 358px;
                    background: 0;
                    border: 0;
                    color: $black;
                    font-family: $font-primary;
                    font-size: 15px;
                    line-height: 18px;
                    font-weight: 400;
                    margin-bottom: 40px;  
                    border-bottom: 1px solid #000;
                    padding: 0 0 10px 0;      
                    &:focus-visible{
                        outline: none;
                    }
                }
            }   
            .btn{
                width: 270px;
                margin-top: 10px;
                margin-left: 60px;
                padding: 22px 23px 21px 24px;
                &:hover {
                    padding: 22px 22px 21px;
                }
                &:focus {
                    padding: 22px 22px 21px;
                }
            }           
        }
        .card-block{
            position: absolute;  
            top: 98px;
            left: 29px;    
            width: 652px;
            height: 382px;
            background: #fff;
            padding: 60px;
            display: none;
            flex-direction: column;
            &-title{
                font-weight: 600;    
                margin-bottom: 40px;  
            }
            &-description{
                margin-bottom: 20px;
                p{
                    font-size: 18px;
                    line-height: 1;
                    margin-bottom: 30px;
                } 
            }
            .btn{
                width: 200px;
                margin-left: 0;
                margin-right: 335px;   
                padding: 19px 35px;
                &:hover{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
                &:focus{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
            }                  
        } 

        .dm-advice-img{
            height: 577px;
            width: 730px;
            position: absolute;
            top: 0;
            right: 0;
        }
    }    
}