.s-decorating{
    .container{
        padding: 0;
        display: flex;
        .decorating-info{
            padding: 100px 0 100px 100px;
            .decorating-title{
                font-weight: 600;
                line-height: 24px;
                margin-bottom: 50px;
                margin-right: 541px;
                position: relative;
                &::before{
                    content: "";
                    width: 100%;
                    height: 0.5px;
                    background: $third-color;
                    position: absolute;
                    bottom: -10px;
                    left: 0;                
                }
            }
            .decorating-description{
                margin-bottom: 50px;
                margin-right: 64px;  
                p{
                    font-size: 18px;
                    line-height: 23px;  
                    padding-bottom: 20px;
                    margin: 0;
                    &:last-child{
                        padding-bottom: 0px;
                    }          
                }                
            }
            .btn{
                width: 200px;
                padding: 19px 45px 18px 44px;
                &:hover{
                    padding: 19px 44px 18px 43px;
                }
                &:focus {
                    padding: 19px 44px 18px 43px;
                }                 
            }
        }
    }    
}

