.rcSmonly-want-also{
    .container{
        padding: 95px 352px 0 100px;
        width: 1440px;
        height: 378px;
        background: url(assets/img/rcSmonly-want-also-background.png) center no-repeat;
        display: flex;
        justify-content: space-between;
        position: relative;
        &::before{
            content: "";
            width: 1440px;
            height: 754px;
            background: rgba(23, 23, 23, .9);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 0;
        }

        .rcSmonly-wa{
            width: 511px;
            position: relative;
            &-title{
                color: $white;
                font-weight: 600;
                margin-bottom: 40px;
            }
            &-description{
                font-size: 18px;
                line-height: 1;
                color: $white;
                font-weight: 300; /*400*/
            }
        }
        .your-project-form{    
            display: flex;
            flex-direction: column;    
            position: relative;
            z-index: 1;        
            &-item{
                input[type="text"], input[type="tel"]{
                    width: 358px;
                    background: 0;
                    border: 0;
                    color: $white;
                    font-family: $font-primary;
                    font-size: 15px;
                    line-height: 1;
                    margin-bottom: 30px;  
                    border-bottom: 1px solid $white;
                    padding: 0 0 10px 0;      
                    &:focus-visible{
                        outline: none;
                    }
                }
            }             
            .btn{
                max-width: 250px;
                line-height: 1;
                padding: 21px 17px 21px;
                &:hover {
                    padding: 21px 15px 21px 14px;
                }
                &:focus {
                    padding: 21px 15px 21px 14px;
                }
            }      
        }
        .card-block{
            position: absolute;  
            top: 0px;
            right: 0px;   
            width: 1440px;
            height: 378px;
            background: #fff;
            padding: 60px 453px;
            display: none;
            flex-direction: column;            
            &-title{
                font-weight: 600;    
                margin-bottom: 40px;  
            }
            &-description{
                margin-bottom: 20px;
                p{
                    font-size: 18px;
                    line-height: 1;
                    margin-bottom: 30px;
                } 
            }
            .btn{
                margin-left: 0;
                margin-right: 335px;   
                padding: 19px 35px;
                &:hover{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
                &:focus{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
            }                  
        }   
    }
    
}