.ae-stages{
    .container{
        padding: 100px;
        width: 1440px;
        height: 100%;
        background: url(assets/img/ae-stages-background.png) center no-repeat;
        position: relative;
        &::before{
            content: "";
            width: 1440px;
            height: 100%;
            background: rgba(23, 23, 23, .9);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 0;
        }

        .ae-stages-nav-list{
            display: flex;
            position: relative;
            z-index: 1;
            margin-bottom: 80px;
            .ae-stages-nav-item{
                &::before{
                    content: "";
                    height: 0;
                }
                .ae-stages-nav-link{
                    color: $white;
                    text-decoration: none;
                    font-weight: 600;
                    font-size: 30px;
                    font-family: $font-primary;
                    line-height: 1;
                    margin-right: 100px;                    
                    position: relative;
                    span{
                        width: 60px;
                        height: 5px;
                        background: url(assets/img/arrow-right-white-2.png) center no-repeat;
                        position: absolute;
                        top: 18px;
                        right: -80px;
                    }
                    &:hover{
                        text-decoration: none;
                        opacity: .7;
                        font-family: $font-primary;
                    }
                    &.active-ae-stages{
                        font-weight: 700;
                        padding-bottom: 5px;
                        box-shadow: inset 0 -.3px 0 0 $third-color;
                        font-family: $font-primary;
                    }
                }
            }
        }
        .hidden{
            display: none;
        }
        .stages-design{
            position: relative; 
            z-index: 1;       

            .stages-title{
                position: relative;
                z-index: 1;
                color: $third-color;
                font-weight: 600;
                font-size: 25px;
                margin-bottom: 49px;
            }
            &-list{
                margin: 0 100px 0 62px;     
                .stages-design-item{
                    margin-bottom: 21px;
                    padding-left: 0;
                    position: relative;
                    &::before{
                        content: counter(li, decimal) "";
                        counter-increment: li;
                        margin-left: -45px;
                        margin-right:0px;
                        font-weight: 600;
                        font-size: 15px;
                        line-height: 18px;
                        color: $white;
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                    &::after{
                        content: "";
                        width: 40px;
                        height: 40px;
                        border: .5px solid #fff;
                        position: absolute;
                        top: -10px;
                        left: -60px;                       
                    }
                    &:last-child{
                        margin-bottom: 11px;
                    }
                    .stages-design-title{
                        font-weight: 600;
                        font-size: 20px;
                        line-height: 1;
                        color: $white;
                        margin-bottom: 20px;
                    }
                    .stages-design-desciption{
                        padding-bottom: 21px;
                        height: 100%;
                        border-left: .5px solid #fff;
                        margin-left: -40px;
                        padding-left: 40px;
                        p{
                            font-weight: 300;
                            font-size: 18px; 
                            line-height: 20px;
                            color: $white;
                            margin-bottom: 10px; 
                            &:last-child{
                                margin-bottom: 0px; 
                            }
                        }                      
                    }
                }
            }
        }

        .stages-construction{           
            position: relative;
            z-index: 1;
            .stages-title{
                color: $third-color;
                font-weight: 600;
                font-size: 25px;
                margin-bottom: 49px;
            }
            &-info{
                p{
                    color: $white;
                    font-size: 18px;
                    margin-bottom: 20px;
                }
                .stages-construction-list{
                    margin: 30px 0;
                    .stages-construction-item{
                        color: $white;
                        margin-left: 14px;
                        font-size: 18px;
                        font-weight: 300; /*400*/
                        &::before{
                            content: "";
                            background-color:$white;
                            border-radius: 0;
                            width: 9px;
                            min-width: 9px;
                            height: 2px;
                            top: 11px;
                            left: -14px;

                        }
                    }
                }
            }
        }
        .stages-landscape{
            position: relative;
            z-index: 1;
            .stages-title{
                color: $third-color;
                font-weight: 600;
                font-size: 25px;
                margin-bottom: 49px;
            }
            &-info{
                p{
                    color: $white;
                    font-size: 18px;
                    margin-bottom: 20px;
                    font-weight: 300; /*400*/
                }
            }            
        }

        .accordion{
            display: none;
        }
    }
}
