.how-we-work{
    .container{
        padding: 0;
        display: flex;
        .hww-img{
            display: flex;
            flex-direction: column;
        }
        .hww{
            background: $primary-color;
            &-title{
                font-weight: 600;
                line-height: 36px;
                color: $white;
                padding: 100px 0 71px 416px;
                margin-bottom: 0;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(180deg);
                    position: absolute;
                    bottom: 50px;
                    right: 100px;
                }
            }
            &-info{
                &-list{
                    margin: 0 100px 0 105px;                
                    .hww-info-item{
                        margin-bottom: 21px;
                        padding-left: 0;
                        position: relative;
                        &::before{
                            content: counter(li, decimal) "";
                            counter-increment: li;
                            margin-left: -45px;
                            margin-right:0px;
                            font-weight: 600;
                            font-size: 15px;
                            line-height: 18px;
                            color: $white;
                            position: absolute;
                            top: 0;
                            left: 0;
                        }
                        &::after{
                            content: "";
                            width: 40px;
                            height: 40px;
                            border: .5px solid #fff;
                            position: absolute;
                            top: -10px;
                            left: -60px;                       
                        }
                        &:last-child{
                            margin-bottom: 11px;
                        }
                        .hww-info-title{
                            font-weight: 600;
                            line-height: 25px;
                            color: $white;
                            margin-bottom: 20px;
                        }
                        .hww-info-desciption{
                            font-weight: 300;
                            font-size: 18px;
                            line-height: 20px;
                            color: $white;
                            padding-bottom: 39px;
                            height: 100%;
                            border-left: .5px solid #fff;
                            margin-left: -40px;
                            padding-left: 40px;
                        }
                    }
                }
            }
            .btn-modal{
                width: 250px;
                margin-left: 105px;
                font-weight: 500;   
                padding: 18px 45px 18px 46px;                 
                &:hover{
                    font-weight: 700;   
                    padding: 18px 43px 18px; 
                }  
                &:focus{
                    font-weight: 700;   
                    padding: 18px 43px 18px; 
                }  
            }

            .modal{
                top: 100px;
                &-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                            .btn-close{
                                &:focus{
                                    box-shadow: none;
                                }
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 39px;
                            &-description{
                                font-size: 18px;
                                line-height: 1;
                                margin-bottom: 50px;
                            }
                            &-form{
                                &-item{
                                    input[type="text"], input[type="tel"]{
                                        width: 100%;
                                        max-width: 532px;
                                        background: 0;
                                        border: 0;
                                        color: $black;
                                        font-family: $font-primary;
                                        font-size: 15px;
                                        line-height: 1;
                                        margin-bottom: 30px;  
                                        border-bottom: 1px solid #000;
                                        padding: 0 0 10px 0;      
                                        &:focus-visible{
                                            outline: none;
                                        }
                                    }
                                    input[type="tel"]{
                                        margin-bottom: 120px;  
                                    }
                                } 
                            }                    
                            textarea{
                                width: 100%;
                                max-width: 532px;
                                font-family: $font-primary;
                                font-size: 15px;
                                line-height: 1;
                                border: 0;
                                background: none;
                                padding: 0 0 10px 0;
                                border-bottom: 1px solid #000;
                                &:focus-visible{
                                    outline: none;
                                }
                            }
                        }
                        .modal-footer{                    
                            .btn-send{
                                width: 270px;
                                margin-left: 0;
                                margin-right:267px;   
                                padding: 21px 27px;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 1;
                                &:hover{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                            }
                        }                              
                    }
                }                         
            }
            .modal#exampleModalToggleHwwSent {
                top: 100px;
                .modal-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 20px;
                            &-description{
                                p{
                                    font-size: 18px;
                                    line-height: 1;
                                    margin-bottom: 30px;
                                }                                    
                            }                  
                        }
                        .modal-footer{                    
                            .btn{
                                width: 200px;
                                margin-left: 0;
                                margin-right: 335px;   
                                padding: 19px 35px;
                                &:hover{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                     
            }
        }
    }    
}

 