.fwm-sample-comtract{
    .container{
        padding: 100px 256px 100px 295px;
        width: 1440px;
        height: 328px;
        background: url(assets/img/sample-comtract-background.png) center no-repeat;
        position: relative;
        &::before{
            content: "";
            width: 1440px;
            height: 328px;
            background: rgba(255, 255, 255, 0.6);            
            position: absolute;
            top: 0;
            left: 0;
        }
        .sample-comtract-title{
            line-height: 1;
            font-weight: 600;
            margin-bottom: 60px;
            position: relative;
            span{
                position: relative;
                &::before{
                    content: "";
                    width: 30px;
                    height: 9px;
                    background: url(assets/img/arrow-right.png) center no-repeat;
                    position: absolute;
                    top: 15px;
                    right: -60px;
                }
            }
        }
        .sample-comtract-block{
            position: relative;
            display: flex;
            .sample-comtract-description{
                margin-right: 152px;                
            } 
            .btn{
                width: 220px;
            }
        }
    }    
}

