.idm-composition-design-project{
    .container{
        padding: 100px 100px 88px 205px;
        display: flex;
        flex-direction: column;
        position: relative;
        .composition-design-project-title{
            font-weight: 600;
            line-height: 1;
            margin-bottom: 50px;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                position: absolute;
                top: -19px;
                left: -19px;                
            }
        }
        .composition-design-project-info{
            display: flex;
            .cdp-left{          
                max-width: 517px;
                width: 100%;  
                margin-right: 57px;
                &-title{
                    font-weight: 600;
                    line-height: 1;
                    margin-bottom: 30px;
                }
                &-list{
                    .cdp-left-item{
                        font-size: 15px;
                        line-height: 1;
                        padding-left: 24px;
                        margin-bottom: 15px;
                        &:last-child{
                            margin-bottom: 0;
                        }
                    }
                }            
            }
            .cdp-right{
                width: 100%;
                max-width: 560px;
                &-title{
                    font-weight: 600;
                    line-height: 1;
                    margin-bottom: 30px;
                }
                &-list{
                    margin-bottom: 39px;
    
                    .cdp-right-item{
                        font-size: 15px;
                        line-height: 1;
                        padding-left: 24px;
                        margin-bottom: 15px;
                        &:last-child{
                            margin-bottom: 0px;
                        }
                    }
                }
                .cdp-form{
                    display: flex;
                    flex-direction: column;
                    width: 560px;
                    height: 387px;
                    background:  url(assets/img/cdp-background.png) center no-repeat;
                    position: relative;
                    &::before{
                        content: "";
                        position: absolute;
                        width: 560px;
                        height: 387px;
                        background: rgba(22, 22, 22, 0.8);
                    }
                    &-title{
                        padding-top: 60px;
                        margin: 0 102px 50px;
                        color: $white;
                        font-weight: 600;
                        line-height: 1;
                        position: relative;
                        z-index: 0;
                    }
                    &-item{
                        position: relative;
                        z-index: 0;
                        margin-left: 102px;
                        input[type="text"], input[type="tel"]{
                            width: 358px;
                            background: 0;
                            border: 0;
                            color: $black;
                            font-family: $font-primary;
                            font-size: 15px;
                            line-height: 18px;
                            font-weight: 400;
                            margin-bottom: 30px;  
                            border-bottom: 1px solid #fff;
                            padding: 0 0 10px 0;      
                            &:focus-visible{
                                outline: none;
                            }
                        }
                    }   
                    .btn{
                        position: relative;
                        width: 250px;
                        margin-left: 102px;
                        padding: 22px 35px 21px 26px;
                        &:hover {
                            padding: 22px 35px 21px 26px;
                        }
                        &:focus {
                            padding: 22px 35px 21px 26px;
                        }
                    }   
                }  
                .modal{
                    position: absolute;
                    top: 478px;
                    &-dialog{
                        max-width: 560px;
                        max-height: 387px;
                        height: 100%;
                        right: -338px;
                        .modal-content{
                            background:  url(assets/img/cdp-background.png) center no-repeat;
                            padding: 60px 60px 60px 100px;
                            border-radius: 0;
                            position: relative;
                            &::before{
                                content: "";
                                width: 560px;
                                height: 387px;
                                background: rgba(22, 22, 22, 0.8);
                                position: absolute;
                                top: -1px;
                                right: 0px;
                            }
                            .modal-header{
                                position: relative;
                                margin-bottom: 30px;
                                .modal-title{
                                    font-weight: 600;   
                                    line-height: 1; 
                                    padding-right: 50px;
                                    color: #fff;   
                                }
                            }
                            .modal-body{
                                position: relative;
                                margin-bottom: 50px;
                                &-description{
                                    margin-bottom: 0;
                                    p{
                                        font-size: 18px;
                                        line-height: 1;
                                        margin-bottom: 0;
                                        color: #fff;
                                        &:last-child{
                                            margin-bottom: 0;
                                        }
                                    } 
                                }
                            }
                            .modal-footer{
                                position: relative;
                                .btn{
                                    width: 200px;
                                    margin-right: 197px;
                                    padding: 19px 35px;   
                                    &:hover{
                                        padding: 19px 33px 19px 32px;
                                    }
                                    &:focus{
                                        padding: 19px 33px 19px 32px;
                                    }
                                }  
                            }
                        }
                    }   
                }                  
            }
        }
    }   
}