.asm-advantages{
    .container{
        padding: 0;
        position: relative;
        height: 584px;
        width: 1440px;
        background: url(assets/img/asm-advantages-background.png) center no-repeat;
        &::before{
            content: "";
            width: 1440px;
            height: 584px;
            background: rgba(255, 255, 255, 0.7);
            position: absolute;
            top: 0px;
            left: 0px;
        }
        .asm-advantages-title{
            line-height: 37px;
            font-weight: 600;
            padding-top: 100px;
            margin: 0 480px 2px 120px;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                position: absolute;
                top: 84px;
                left: -20px;              
            }
        }

        .asm-advantages-info{
            position: relative;
            margin-left: 100px; 
            .row{
                margin: 0;
            }
            .col-lg-4{
                padding: 0;
                width: 401px;
                margin-right: 20px;
                margin-top: 50px; /*60px*/
                display: flex;
            }
            .asm-advantages-img{
                margin-right: 45px;
                width: 60px;
                height: 60px;
            }
            .asm-advantages-text{
                &-title{
                    line-height: 37px;
                    font-weight: 600;
                    margin-bottom: 20px; /*30px*/
                }
                &-description{
                    font-size: 15px;
                    line-height: 1;
                    font-weight: 400;
                }
            }
        }
    }
    
}