.ae-projects-of-country-houses{
    .container{
        padding: 100px 100px 169px;
        background: $black;
        .ae-pch-title{
            color: $white;
            font-weight: 600;
            margin-bottom: 60px;
            margin-left: 20px;
            position: relative;               
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                transform: rotate(00deg);
                position: absolute;
                top: -20px;
                left: -20px;
            }
        }
        .slider{
            width: 100%;
            z-index: 2;
            position: relative;            
            &-item{ 
                margin: 0;
                justify-content: space-between;
                .ae-pch-link{
                    padding: 0;
                    width: 295px;
                    position: relative;
                    margin-bottom: 20px;
                    &:hover::before{
                        content: "";
                        width: 295px;
                        height: 295px;
                        background: rgba(0, 0, 0, 0.5);
                        position: absolute;
                        top: 0px;
                        left: 0px;
                        z-index: 1;
                    }
                    &:hover .ae-pch-name{
                        display: block;
                    }
                    .ae-pch-name{
                        display: none;
                        position: absolute;
                        top: 138px;
                        left: 55px;
                        color: $white;
                        font-size: 20px;
                        line-height: 24px;
                        font-weight: 600;
                        z-index: 1;
                    }
                }          
            } 
            .slides-numbers {
                color: #fff;
                position: absolute;
                bottom:-73px;
                right: 60px;
                z-index: 3;
                .active {
                    font-size: 20px;
                    line-height: 24px;
                    margin-right: 4px;
                    position: relative;
                    &::after{
                        content: "/";
                        position: absolute;
                        width: 8px;
                        top:0;
                        right: -9px;
                    }
                }      
                .total {
                    font-size: 15px;
                    line-height: 18px;
                }
            }
            .slick-arrow {
                width: 50px;
                height: 10px;
                background: url("assets/img/icon-arrow.png") center/contain no-repeat;
                position: absolute;
                z-index: 1;
                &::before{
                    content: "";
                }
                &.slick-prev {
                    transform: rotate(180deg);
                    left: 1053px;
                    top: 353px;
                }            
                &.slick-next {
                    top: 357px;
                    right: 0px;
                }
                &:hover{
                    opacity: 0.7;
                }        
            } 
        }
        .ae-pch-block-mob{
            display: none;
        }      
    }
}