footer{
    .container{
        padding: 100px 0 100px 100px;
        background: $primary-color;
        display: flex;
        .footer-logo{
            width: 208px;
            margin-right: 107px;
            &-img{
                margin-bottom: 26px;
            }
            &-info{
                color:#707070;
                font-size: 11px;
                line-height: 13px;
                position: relative;
                &::after{
                    content: "";
                    width: 10px;
                    height: 10px;
                    background: url(assets/img/footor-icon-copyright.png) center no-repeat;
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
                span:first-child{
                    padding-left: 16px;
                }
            }
        }
        .btn-modal {
            display: none;
        }
        .footer-links{
            display: flex;
            margin-right: 83px;
            &-column{
                display: flex;
                flex-direction: column;
                margin-right: 41px;
                .footer-link{
                    color:#707070;
                    font-size: 13px;
                    line-height: 16px;
                    margin-bottom: 25px;
                    &:hover{
                        text-decoration: none;
                        opacity: .7;
                    }
                    &:nth-child(5){
                        margin-bottom: 0px;
                    }
                }
            }
            &-column-two{
                display: flex;
                flex-direction: column;
                .footer-link{
                    color:#707070;
                    font-size: 13px;
                    line-height: 16px;
                    margin-bottom: 25px;
                    &:hover{
                        text-decoration: none;
                        opacity: .7;
                    }
                    &:nth-child(7){
                        margin-bottom: 0px;
                    }
                }
            }
        }
        .footer-social-media{
            display: flex;
            margin-right: 72px;
            &-column{
                display: flex;
                flex-direction: column;
                margin-right: 79px;
                &:last-child{
                    margin-right: 0px;
                }
                .footer-social-media-item{
                    color:#707070;
                    font-size: 13px;
                    line-height: 16px;
                    margin-bottom: 22px;
                    position: relative;
                    &.icon-email::before{
                        content: "";
                        width: 15px;
                        height: 15px;
                        background: url(assets/img/footer-icon-email.png) center no-repeat;
                        position: absolute;
                        top: 0;
                        left: -25px;
                    }
                    &.icon-inst::before{
                        content: "";
                        width: 15px;
                        height: 15px;
                        background: url(assets/img/footer-icon-inst.png) center no-repeat;
                        position: absolute;
                        top: 0;
                        left: -25px;
                    }
                    &.icon-vk::before{
                        content: "";
                        width: 15px;
                        height: 15px;
                        background: url(assets/img/footer-icon-vk.png) center no-repeat;
                        position: absolute;
                        top: 0;
                        left: -25px;
                    }
                    &.icon-telegram::before{
                        content: "";
                        width: 15px;
                        height: 15px;
                        background: url(assets/img/footer-icon-telegram.png) center no-repeat;
                        position: absolute;
                        top: 0;
                        left: -25px;
                    }
                    &.icon-house::before{
                        content: "";
                        width: 15px;
                        height: 15px;
                        background: url(assets/img/footer-icon-house.png) center no-repeat;
                        position: absolute;
                        top: 0;
                        left: -25px;
                    }
                    &.icon-pinter::before{
                        content: "";
                        width: 15px;
                        height: 15px;
                        background: url(assets/img/footer-icon-pint.png) center no-repeat;
                        position: absolute;
                        top: 0;
                        left: -25px;
                    }
    
                }
            }
        }
        .footer-contacts{
            &-item{
                color:#707070;
                font-size: 13px;
                line-height: 16px;
                margin-bottom: 29px;
                position: relative;
                .city, .phone{
                    margin-bottom: 10px;
                }
                &.icon-local::before{
                    content: "";
                    width: 15px;
                    height: 15px;
                    background: url(assets/img/footer-icon-local.png) center no-repeat;
                    position: absolute;
                    top: 0;
                    left: -25px;
                }
                &.icon-phone::before{
                    content: "";
                    width: 15px;
                    height: 15px;
                    background: url(assets/img/footer-icon-phone.png) center no-repeat;
                    position: absolute;
                    top: 0;
                    left: -25px;
                }   
            
            }
        }
    }
    
}

 