header{
    .container{
        padding: 0;
    }
    nav.navbar{
        background-color: #303030;
        .container-fluid{
            padding: 0;
            position: relative;
        }
        .navbar-brand{
            margin-right: 0;
            img{
                position: absolute;
                top: 30px;
                left: 0px;
            }
        }
        .navbar-collapse{
            position: relative;
            margin-left: 365px;
            height: 103px;
            .navbar-nav{
                .nav-item{
                    padding-left: 0;
                    margin-right: 35px;
                    &:last-child{
                        margin-right: 0;
                    }
                    &::before{
                        content: "";
                        height: 0;                          
                    }
                    .nav-link{
                        font-size: 16px;
                        line-height: 20px;
                        color: $white;
                        text-transform: uppercase;
                        padding: 0;
                        &:hover{
                            text-decoration: none;
                            opacity: .8;
                        }
                        &.active{
                            padding-bottom: 10px;
                            box-shadow: inset 0 -.3px 0 0 #DF9D60;
                        }  
                    }
                }
            }
            .town{
                margin-left: 154px;
                width: 136px;
                font-size: 15px;
                line-height: 20px;
                color: $white;
            }
            .btn-primary{
                margin-left: 40px;
                width: 180px;
            }           
            .modal{
                top: 100px;
                &-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                            .btn-close{
                                &:focus{
                                    box-shadow: none;
                                }
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 39px;
                            &-description{
                                font-size: 18px;
                                line-height: 1;
                                margin-bottom: 50px;
                            }
                            &-form{
                                &-item{
                                    input[type="text"], input[type="tel"]{
                                        width: 100%;
                                        max-width: 532px;
                                        background: 0;
                                        border: 0;
                                        color: $black;
                                        font-family: $font-primary;
                                        font-size: 15px;
                                        line-height: 1;
                                        margin-bottom: 30px;  
                                        border-bottom: 1px solid #000;
                                        padding: 0 0 10px 0;      
                                        &:focus-visible{
                                            outline: none;
                                        }
                                    }
                                    input[type="tel"]{
                                        margin-bottom: 120px;  
                                    }
                                } 
                            }                    
                            textarea{
                                width: 100%;
                                max-width: 532px;
                                font-family: $font-primary;
                                font-size: 15px;
                                line-height: 1;
                                border: 0;
                                background: none;
                                padding: 0 0 10px 0;
                                border-bottom: 1px solid #000;
                                &:focus-visible{
                                    outline: none;
                                }
                            }
                        }
                        .modal-footer{                    
                            .btn-send{
                                width: 270px;
                                margin-left: 0;
                                margin-right:267px;   
                                padding: 21px 27px;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 1;
                                &:hover{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                         
            }
            .modal#exampleModalToggleHeaderSent {
                top: 100px;
                .modal-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 20px;
                            &-description{
                                p{
                                    font-size: 18px;
                                    line-height: 1;
                                    margin-bottom: 30px;
                                }                                    
                            }                  
                        }
                        .modal-footer{                    
                            .btn{
                                width: 200px;
                                margin-left: 0;
                                margin-right: 335px;   
                                padding: 19px 35px;
                                &:hover{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                     
            }          

            .menu-toggler{
                margin-top: 25px;
                margin-left: 31px;
                display: flex;
                flex-direction: column;
                .burger-name{
                    font-size: 15px;
                    line-height: 18px;
                    font-weight: 600;
                    color: #fff;
                    text-transform: uppercase;
                    margin-top: -28px;
                    margin-left: -6px;                    
                }
                width: 54px;
                height:20px;
                position: relative;
                border: none;
                background: none;
                cursor: pointer;
                span {
                    width: 100%;
                    height: 1px;
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    background-color: #fff;
                    &:nth-child(2) {
                        top: 50%;
                    }
                    &:nth-child(3) {
                        top: 100%;
                    }
                }
                &.active{
                    span{
                        width: 25px;
                        margin-left: 29px;
                        transform: rotate(45deg) translate(0px, 0px);
                    }
                }
                &.active{
                    span:nth-child(2) {
                        opacity: 0;
                    }
                } 
                &.active{
                    span:nth-child(3) {
                        width: 25px;
                        margin-left: 29px;
                        transform: rotate(-45deg) translate(14.5px, -13.5px);
                    }   
                } 
                &.active .burger-name{
                    display: none;
                }
            }                    
            .menu-toggler.active + .nav-menu{
                display: block;
            }
            .nav-menu{
                display: none;
                width: 350px;
                position: absolute;
                top: 85px;
                right: 100px;
                background-color: rgba(48, 48, 48, 0.9);
                z-index: 1;
                &-list{ 
                    margin: 40px 60px;                                            
                    .nav-menu-item{
                        padding: 0;
                        margin-bottom: 25px;
                        &::before{
                            content: "";
                            height: 0;
                        }
                        .nav-menu-link{
                            color: #FFFFFF;
                            font-size: 18px;
                            line-height: 22px; 
                            &:hover{
                                text-decoration: none;
                                opacity: .8;   
                            } 
                            &.active-menu{
                                padding-bottom: 10px;
                                box-shadow: inset 0 -.3px 0 0 $third-color;
                            }                            
                        }
                        &:nth-child(3), &:nth-child(6){
                            border-top: .3px solid rgba(255, 255, 255, 1);
                            padding-top: 30px;
                        }
                        &:nth-child(5){
                            margin-bottom: 30px;
                        }
                    }  
                }
                .more-list{
                    margin-top: 31px;
                    .more-item{
                        margin-bottom: 25px;
                        &::before{
                            content: "";
                            height: 0;                                
                        }
                        .more-link{
                            color: #fff;
                            font-size: 16px;
                            line-height: 20px;
                            &:hover{
                                text-decoration: none;
                                opacity: .8; 
                            } 
                            &.active-more{
                                color: $third-color;
                                font-weight: 600;
                            } 
                            &.active-more{
                                color: $third-color;
                                font-weight: 600;
                            }
                        }
                    }
                }
            }                
        }         
    }    
}