.rcSmonly-more{
    .container{
        padding: 100px 100px 0 120px;
        display: flex;
        width: 1440px;
        height: 754px;
        background: url(assets/img/rcSmonly-more-background.png) center no-repeat;
        position: relative;
        &::before{
            content: "";
            width: 1440px;
            height: 754px;
            background: rgba(23, 23, 23, .9);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 0;
        }
        .rcSmonly-more-info{
            position: relative;
            margin-right: 285px;
            &-title{
                color: $white;
                font-weight: 600;
                margin-bottom: 60px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(0deg);
                    position: absolute;
                    top: -20px;
                    left: -20px;
                }
            }
            &-what {
                color: $third-color;
                font-weight: 600;
                margin-bottom: 100px;
            }
            &-subtitle{
                color: $white;
                font-weight: 600;
                margin-bottom: 30px;
            }
            &-description{
                color: $white;
                font-size: 16px; /*18px*/
                font-weight: 300;
                margin-bottom: 60px;
            }
        }
        .rcSmonly-more-img{
            position: relative;
            z-index: 0;
            width: 400px;
            height: 460px;
        }
    }
}