.fwm-insurance-and-warranty{
    .container{
        padding: 0;
        display: flex;
        .insurance-and-warranty-img-mob{
            display: none;
        }
        .iw-info{
            padding: 100px 83px 81px 100px;
            .iw-title{
                line-height: 1;
                font-weight: 600;
                margin-bottom: 72px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(0deg);
                    position: absolute;
                    top: -20px;
                    left: -20px;                
                }
            }
            .iw-item{
                margin-bottom: 60px;
                &:last-child{
                    margin-bottom: 0;
                }
                &-title{
                    font-weight: 600;
                    line-height:1;
                    margin-bottom: 20px;
                }
                &-description{
                    font-size: 18px;
                    line-height:1;
                    p{
                        font-size: 18px;
                        line-height:22px;
                        padding-bottom: 0 0 10px 0;
                    }
                }
            }
        }
        .insurance-and-warranty-img{
            display: block;
        }        
    }    
}