.fwm-advantages{
    .container{
        padding: 100px 0 66px 120px;
        height: 661px;
        width: 1440px;
        background: url(assets/img/advantages-fwm-background.png) center no-repeat;
        position: relative;
        &::before{
            content: "";
            width: 1440px;
            height: 661px;
            background: rgba(255, 255, 255, 0.5);
            position: absolute;
            top: 0px;
            left: 0px;
        }       

        .fwm-advantages-title{
            font-weight: 600;
            line-height: 1;
            margin-bottom: 2px;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                transform: rotate(-90deg);
                position: absolute;
                top: -20px;
                left: -20px;                
            }
        }
        .fwm-advantages-info{
            position: relative;
            .row{
                margin: 0;
            }
            .col-lg-4{
                padding: 0;
                display: flex;
                width: 385px;
                margin-right: 35px;
                margin-top: 79px;
            }
            .fwm-advantages-img{
                margin-right: 30px;
                width: 60px;
                height: 60px;
                &.icon-advantages-seven{
                    width: 61px;
                    height: 41px;
                }
            }
            .fwm-advantages-text{
                &-title{
                    font-weight: 600;
                    line-height: 1;
                    margin-bottom: 30px;
                }
                &-deacription{
                    font-size: 15px;
                    line-height: 17px;        
                }
            }            
        }
    }    
}