.idm-your-project{
    .container{
        padding: 0;
        display: flex;
        height: 713px;
        position: relative;
        .idm-your-project-form{
            padding: 100px 213px 100px 60px;
            display: flex;
            flex-direction: column;
            .idm-your-project-title{
                font-weight: 600;
                line-height: 1;
                margin-bottom: 40px;
                span{
                    position: relative;
                    &::before{
                        content: "";
                        width: 30px;
                        height: 9px;
                        background: url(assets/img/arrow-right.png) center no-repeat;
                        position: absolute;
                        top: 15px;
                        right: -60px;
                    }
                }    
            }
            .idm-your-project-description{
                font-size: 18px;
                line-height: 21px;
                margin-bottom: 50px;
            }
            &-item{
                input[type="text"], input[type="tel"]{
                    width: 358px;
                    background: 0;
                    border: 0;
                    color: $black;
                    font-family: $font-primary;
                    font-size: 15px;
                    line-height: 1;
                    margin-bottom: 30px;  
                    border-bottom: 1px solid #000;
                    padding: 0 0 10px 0;      
                    &:focus-visible{
                        outline: none;
                    }
                }
                input[type="tel"]{
                    margin-bottom: 120px;  
                }
            } 
            textarea{
                width: 358px;
                font-family: $font-primary;
                font-size: 15px;
                line-height: 1;
                border: 0;
                background: none;
                padding: 0 0 20px 0;
                border-bottom: 1px solid #000;
                margin-bottom: 50px;
                &:focus-visible{
                    outline: none;
                }
            } 
            .btn{
                max-width: 270px;
                line-height: 1;
                padding: 21px 27px 21px;
                &:hover {
                    padding: 21px 24px 21px 24px;
                }
                &:focus {
                    padding: 21px 24px 21px 24px;
                }
            } 
        }
        .card-block{
            position: absolute;  
            top: 147px;
            right: 81px;   
            width: 652px;
            height: 419px;
            background: #fff;
            padding: 60px;
            display: none;
            flex-direction: column;
            &-title{
                font-weight: 600;    
                margin-bottom: 40px;  
            }
            &-description{
                margin-bottom: 20px;
                p{
                    font-size: 18px;
                    line-height: 1;
                    margin-bottom: 30px;
                } 
            }
            .btn{
                margin-left: 0;
                margin-right: 335px;   
                padding: 19px 35px;
                &:hover{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
                &:focus{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
            }                  
        }  
    }
}