.dfm-advice{
    .container{
        padding: 100px;
        height: 577px;
        width: 1440px;
        background: url(assets/img/dfm-advice-background.png) center no-repeat;
        display: flex;
        justify-content: space-between;
        position: relative;

        .dfm-advice-info{
            width: 505px;
            &-title{
                color: $white;
                font-weight: 600;
                margin-bottom: 60px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(90deg);
                    position: absolute;
                    top: -20px;
                    right: 26px;                
                }
            }
            &-description{
                
                p{
                    color: $white;
                    font-size: 15px;
                    margin-bottom: 10px;
                }
                
            }
        }
        .dfm-advice-form{
            display: flex;
            flex-direction: column;
            width: 478px;
            height: 419px;
            background: rgba(255, 255, 255, 1);
            &-title{
                font-weight: 600;
                line-height: 37px;
                padding: 60px 60px 50px;
            }
            &-item{
                margin-left: 60px;
                input[type="text"], input[type="tel"]{
                    width: 358px;
                    background: 0;
                    border: 0;
                    color: $black;
                    font-family: $font-primary;
                    font-size: 15px;
                    line-height: 18px;
                    font-weight: 400;
                    margin-bottom: 40px;  
                    border-bottom: 1px solid #000;
                    padding: 0 0 10px 0;      
                    &:focus-visible{
                        outline: none;
                    }
                }
            }   
            .btn{
                width: 270px;
                margin-top: 10px;
                margin-left: 60px;
                padding: 19px 23px 19px 24px;
                &:hover {
                    padding:19px 22px;
                }
                &:focus {
                    padding: 19px 22px;
                }
            }           
        }
        .card-block{
            position: absolute;  
            top: 79px;
            right: 100px;    
            width: 606px;
            height: 382px;
            background: #fff;
            padding: 60px;
            display: none;
            flex-direction: column;
            &-title{
                font-weight: 600;    
                margin-bottom: 40px;  
            }
            &-description{
                margin-bottom: 20px;
                p{
                    font-size: 18px;
                    line-height: 1;
                    margin-bottom: 30px;
                } 
            }
            .btn{
                width: 200px;
                margin-left: 0;
                margin-right: 335px;   
                padding: 19px 35px;
                &:hover{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
                &:focus{
                    font-weight: 700;
                    padding: 19px 32px 19px 33px;                                    
                }
            }                  
        } 
    }   
}