.we-offer{
    .container{
        padding: 0;
        display: flex;
        .we-offer-block{
            padding: 100px 30px 100px 101px;
            .we-offer-title{
                margin-bottom: 11px;
                font-weight: 600;
                line-height: 37px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(180deg);
                    position: absolute;
                    bottom: -20px;
                    right: 355px;
                }
            }
            .we-offer-item{
                margin-top: 60px;
                &-title{
                    font-weight: 600;
                    line-height: 24px;
                    padding-bottom: 20px;
                }
                &-info{
                    display: flex;                   
                    &-description{
                        width: 460px;
                        font-weight: 300;
                        font-size: 18px;
                        line-height: 22px;
                        margin-right: 65px;   
                    }
                    .btn-secondary{
                       width: 180px;
                    }
                }
                .offer-img-mob{
                    display: none;
                }
            }
        }
        .offer-img{
            margin-left: -3px;
        }
    }       
}