.reviews{
    height: 555px;
    .container{
        padding: 0;
        position: relative;
        &::after{
            content: "";
            width: 1440px;
            height: 555px;
            background: url(assets/img/reviews-after.png) center no-repeat;
            position: absolute;
            top: 0px;
            left: 0px;
            z-index: -1;
        }
        .reviews-title{
            font-weight: 600;
            line-height: 37px;
            color: $white;
            padding-top: 100px;
            margin: 0 0 60px 121px;
            position: relative;
            &::after{
                content: "";
                width: 70.5px;
                height: 70.5px;
                background: url(assets/img/union-icon.png) center no-repeat;
                position: absolute;
                top: 80px;
                left: -20px;             
            }    
        }
        .review{
            display: flex;
            margin: 0 205px 50px;
            &-item{
                display: flex;
                flex-direction: column;  
                margin-right: 50px;    
                &-text{
                    font-size: 15px;
                    line-height: 18px;
                    color: $white;
                    font-weight: 300;  
                    margin-bottom: 20px;  
                    height: 122px;  
                    width: 220px;          
                }              
                &:last-child{
                    margin-right: 0px; 
                }
                .who{
                    i{
                        font-size: 15px;
                        line-height: 18px;
                        color: $white; 
                        font-weight: 300;
                    }
                }
            }
        }
        .review-mob{
            display: none;
        }
        .btn{
            margin-left: 985px;
            width: 250px;
        }
    }
    
}