.advantages{
    .container{
        padding: 0;
        position: relative;
        width: 100%;
        max-width: 1440px;
        height: 713px;
        background: url(assets/img/advantages-background.png) center no-repeat;
        &::before{
            content: "";
            width: 100%;
            max-width: 1440px;
            height: 713px;
            background: rgba(255, 255, 255, 0.5);
            position: absolute;
            top: 0px;
            left: 0px;
        }
        .advantages-title{
            line-height: 37px;
            font-weight: 600;
            padding-top: 100px;
            margin-left: 120px;
            margin-bottom: 2px;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                transform: rotate(-90deg);
                position: absolute;
                bottom: -20px;
                left: -20px;              
            }
        }            
        .advantages-info{
            position: relative;
            margin-left: 100px;
            .row{
                margin: 0;
            }
            .col-lg-4{
                padding: 0;
                width: 400px;
                margin-right: 20px;
                margin-top: 79px;
                display: flex;
            }
            .advantages-img{
                margin-right: 45px;
                width: 60px;
                height: 60px;
            }
            .advantages-text{
                &-title{
                    line-height: 37px;
                    font-weight: 600;
                    margin-bottom: 30px;
                }
                &-description{
                    font-size: 15px;
                    line-height: 18px;
                    font-weight: 400;
                }
            }
        }
    }    
}