.idm-technical-project{
    .container{
        padding: 0;
        display: flex;
        .technical-project-info{
            width: 100%; 
            padding: 100px 60px 0 100px;
            background: #fff;
            .technical-project-top{
                display: flex;
                justify-content: space-between;
                .technical-project-title{
                    font-weight: 600;
                    line-height: 24px;
                    margin-bottom: 10px;
                    border-bottom: .5px solid $third-color; 
                    padding-bottom: 10px;  
                }
                .price{
                    font-weight: 600;
                    line-height: 24px;
                    font-size:20px;
                } 
            }
           
            .technical-project-title-second{
                color: $third-color;
                font-size: 15px;
                line-height: 1;
                font-weight: 700;
                margin-bottom: 40px;
            }
            .technical-project-list{
                margin-left: 15px;
                margin-bottom: 50px;
                .technical-project-item{
                    margin-bottom: 5px;
                    &:last-child{
                        margin-bottom: 0; 
                    }
                    &:before {
                        content: "";
                        background-color: #000;
                        border-radius: 100%;
                        width: 3px;
                        min-width: 3px; 
                        height: 3px;
                        top: 10px;
                        left: -13px;
                    }
                    
                }
            }
            .technical-project-bottom{  
                display: flex;
                justify-content: space-between;           
                .btn-secondary {
                    width: 200px;
                    background-color: #fff;
                    padding: 19px 58px;
                    width: 200px;
                    &:hover {
                        color: #000;
                        background-color: #fff;
                        padding: 19px 56px 19px 57px;
                    }
                    &:focus {
                        padding: 19px 56px 19px 57px;
                        color: #000;
                    } 
                }
            }

            .modal{
                top: 100px;
                &-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                            .btn-close{
                                &:focus{
                                    box-shadow: none;
                                }
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 39px;
                            &-description{
                                font-size: 18px;
                                line-height: 1;
                                margin-bottom: 50px;
                            }
                            &-form{
                                &-item{
                                    input[type="text"], input[type="tel"]{
                                        width: 100%;
                                        max-width: 532px;
                                        background: 0;
                                        border: 0;
                                        color: $black;
                                        font-family: $font-primary;
                                        font-size: 15px;
                                        line-height: 1;
                                        margin-bottom: 30px;  
                                        border-bottom: 1px solid #000;
                                        padding: 0 0 10px 0;      
                                        &:focus-visible{
                                            outline: none;
                                        }
                                    }
                                    input[type="tel"]{
                                        margin-bottom: 120px;  
                                    }
                                } 
                            }                    
                            textarea{
                                width: 100%;
                                max-width: 532px;
                                font-family: $font-primary;
                                font-size: 15px;
                                line-height: 1;
                                border: 0;
                                background: none;
                                padding: 0 0 10px 0;
                                border-bottom: 1px solid #000;
                                &:focus-visible{
                                    outline: none;
                                }
                            }
                        }
                        .modal-footer{                    
                            .btn-send{
                                width: 270px;
                                margin-left: 0;
                                margin-right:267px;   
                                padding: 21px 27px;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 1;
                                &:hover{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                         
            }
            .modal#exampleModalToggleIdmTPSent {
                top: 100px;
                .modal-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 20px;
                            &-description{
                                p{
                                    font-size: 18px;
                                    line-height: 1;
                                    margin-bottom: 30px;
                                }                                    
                            }                  
                        }
                        .modal-footer{                    
                            .btn{
                                width: 200px;
                                margin-left: 0;
                                margin-right: 335px;   
                                padding: 19px 35px;
                                &:hover{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                            }
                        }                   
                    }
                }                     
            }
        }
    }    
}