.aum-our-projects-in-instagram{
    .container{
        padding: 100px;
        background-color: $primary-color;
        .opi-title{
            font-weight: 600;
            line-height: 37px;
            margin: 0 0 60px 403px;
            color: $white;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                transform: rotate(180deg);
                position: absolute;
                top: -13px;
                right: 384px;
            }
        }
        .opi-example{
            .row{
                margin: 0;
            }
            .col-lg-4{
                padding: 0;
                margin-bottom: 20px;
                position: relative;
                &:hover .opi-example-general{
                    display: block;
                }        
            }
            &-general{
                display: none;
                img{
                    width: 400px;
                    height: 50px;
                    position: absolute;
                    bottom:0;
                    left: 0;
                    z-index: 1;
                }                
            }
        }
        .opi-links{
            display: flex;
            margin: 40px 0 0 75px;
            .btn-warning{
                width: 270px;
                padding: 19px 28px 19px 29px;
                &:hover{
                    padding: 19px 24px 19px 25px;
                }
                &:focus{
                    padding: 19px 25px 19px 25px;
                }
            }            
            &-inst{                
                font-size: 20px;
                line-height: 24px;
                color: $white;
                margin: 19px 201px 0 201px;
                position: relative;
                &::before{
                    content: "";
                    width: 40px;
                    height: 40px;
                    background: url(assets/img/aum-opi-icon-inst-links.png) center no-repeat;
                    position: absolute;
                    top: -5px;
                    left: -55px;
                }
            }
            .btn-secondary{
                width: 180px;
                padding: 18px 28px;
                &:hover{
                    padding: 18px 24px 18px 25px;
                }
                &:focus{
                    padding: 18px 25px 18px 25px;
                }
            }
        }
    }   
}