.first-screen{
   .container{
        padding: 0; 
        position: relative;  
        .scrollbottom{
            width: 7px;
            height: 30px;
            background: url(assets/img/arrow.png) center no-repeat;
            position: absolute;
            bottom: 90px;
            left: 163px;
            cursor: pointer;
            z-index: 1;
            &:hover{
                opacity: 0.7;
            }
        }
        .slider{       
            position: relative;               
            &-item{
                width: 100%;
                max-width: 1440px;
                height: 797px;
                &-one{
                    position: relative;
                    &::after{
                        content: "";
                        width: 100%;
                        max-width: 1440px;
                        height: 900px;
                        background: url(assets/img/first-screen-after.png) center no-repeat;
                        position: absolute;
                        top: -103px;
                        left: 0px;
                        z-index: -1;
                    }
                    &::before{
                        content: "";
                        width: 100%;
                        max-width: 1440px;
                        height: 797px;
                        background: url(assets/img/first-screen-before.png) center no-repeat;
                        position: absolute;
                        top: 0px;
                        left: 0px;
                        z-index: 0;
                    } 
                    .first-screen-title{
                        font-family: $font-secondary;
                        font-weight: 300;
                        line-height: 40px;
                        color: $black;
                        padding: 339px 0 0 490px;
                        position: relative;
                        &::after{
                            content: "";
                            width: 332px;
                            height: 35px;
                            background: url(assets/img/first-screen-logo-title.png) center no-repeat;
                            position: absolute;
                            top: 284px;
                            left: 544px;
                        }
                    }                
                }
                &-two, &-three{
                    position: relative;
                    &::after{
                        content: "";
                        width: 100%;
                        max-width: 1440px;
                        height: 797px;
                        background: url(assets/img/first-screen-after.png) center no-repeat;
                        position: absolute;
                        top: 0px;
                        left: 0px;
                        z-index: -1;
                    }
                    .first-screen-title{
                        font-family: $font-secondary;
                        font-weight: 300;
                        line-height: 40px;
                        color: $black;
                        padding: 339px 0 0 490px;
                        position: relative;
                        &::after{
                            content: "";
                            width: 332px;
                            height: 35px;
                            background: url(assets/img/first-screen-logo-title.png) center no-repeat;
                            position: absolute;
                            top: 284px;
                            left: 544px;
                        }
                    }
    
                }
            } 
            .slick-arrow {
                width: 50px;
                height: 10px;
                background: url("assets/img/icon-arrow-black.png") center/contain no-repeat;
                position: absolute;
                bottom: 0;
                z-index: 1;            
                &::before{
                    content: "";
                }
                &.slick-prev {
                    transform: rotate(180deg);
                    left: 1190px;
                    top: 720px;
                }            
                &.slick-next {
                    right: 70px;
                    top:725px;
                }
                &:hover{
                    opacity: 0.7;
                }        
            }
            .slides-numbers {
                color: #000;
                position: absolute;
                bottom: 60px;
                right: 130px;
                z-index: 3;
                .active {
                    font-size: 20px;
                    line-height: 24px;
                    margin-right: 11px;
                    position: relative;
                    &::after{
                        content: "/";
                        position: absolute;
                        width: 8px;
                        top:0;
                        right: -14px;
                    }
                }      
                .total {
                    font-size: 15px;
                    line-height: 18px;
                }
            }              
        }        
    }            
}