.aum-achievements{
    .container{
        padding: 90px 0 169px 0;
        width: 1440px;
        height: 1494px;
        background: url(assets/img/aum-our-achievements-background.png) center no-repeat;
        position: relative;
           &::before{
            content: "";
            width: 1440px;
            height: 1494px;
            background: rgba(23, 23, 23, .9);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 0;
        }
        .aum-achievements-block{
            margin-bottom: 165px;
            .aum-achievements-block-title{
                margin-left: 120px;
                font-weight: 600;
                color: $white;
                margin-bottom: 60px;
                position: relative;
                z-index: 1;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(0deg);
                    position: absolute;
                    top: -20px;
                    left: -20px;
                }
            }
            .slider{
                margin-left: 159px;
                width: 1120px;
                position: relative;
                z-index: 2;             
                &-item{
                    display: flex;     
                    justify-content: space-between;                   
                    .aum-achievements-item{
                        width: 250px;
                        height: 448px;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        &-title{
                            color: $white;
                            font-weight: 600;
                        }
                        &-description{
                            color: $white;
                            font-size: 15px;
                            font-weight: 300; /*400*/
                        }
                    }    
                }
                .slick-arrow {
                    width: 50px;
                    height: 10px;
                    background: url("assets/img/icon-arrow.png") center/contain no-repeat;
                    position: absolute;
                    z-index: 1;
                    &::before{
                        content: "";
                    }
                    &.slick-prev {
                        transform: rotate(180deg);
                        left: 1012px;
                        top: 501px;
                    }            
                    &.slick-next {
                        right: -60px;
                        top: 505px;
                    }
                    &:hover{
                        opacity: 0.7;
                    }        
                } 
                .slides-numbers {
                    color: #fff;                    
                    position: absolute;
                    bottom:-70px;
                    right: 0px;
                    z-index: 3;
                    .active {
                        font-size: 20px;
                        line-height: 24px;
                        margin-right: 4px;
                        position: relative;
                        &::after{
                            content: "/";
                            position: absolute;
                            width: 8px;
                            top:0;
                            right: -9px;
                        }
                    }      
                    .total {
                        font-size: 15px;
                        line-height: 18px;
                    }                    
                }
            }
        }
    }
}

