.about-us-more{
    .container{
        padding: 0;
        display: flex;
        .aum-info{
            padding: 97px 55px 0 100px;
            .aum-title{
                font-weight: 600;
                line-height: 37px;     
                margin-bottom: 60px;  
                position: relative;
                &::before{
                    content: "";
                    width: 71px;
                    height: 71px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(180deg);
                    position: absolute;
                    top: -20px;
                    left: 90px;
                }     
            }
            .aum-description{
                p{
                    font-size: 20px;
                    line-height: 23px;
                    margin-bottom: 20px;
                }
            }
            .aum-list{
                font-size: 20px;
                line-height: 23px;
                .aum-item{
                    font-size: 18px;
                    line-height: 22px;
                    margin-bottom: 20px;  /*30px*/
                    margin-left: 19px;
                    &:first-child{
                       margin-top: 40px; 
                    }
                    &:last-child{
                        margin-bottom: 0px;
                    }
                    &::before{
                        content: "";
                        border-radius: 0;
                        width: 9px;
                        height: 9px;
                        background-color: $third-color;
                        left: -19px;
                        
                    }
                }
            }
        }
        .aum-img{
            position: relative;
            &::before{
                content: "";
                width: 710px;
                height: 823px;
                background: url(assets/img/more-before-aum.png) center no-repeat;
                position: absolute;
                top: 0;
                right: 0;
            }
        }
    }    
}