.dm-advantages{
    .container{
        padding: 0;
        position: relative;
        width: 1440px;
        height: 690px;
        background: url(assets/img/dm-advantages-background.png) center no-repeat;
        &::before{
            content: "";
            width: 1440px;
            height: 690px;
            background: rgba(255, 255, 255, 0.7);
            position: absolute;
            top: 0px;
            left: 0px;
        }
        .dm-advantages-title{
            line-height: 37px;
            font-weight: 600;
            padding-top: 100px;
            margin: 0 509px 2px 143px;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                position: absolute;
                top: 84px;
                left: -20px;              
            }
        }

        .dm-advantages-info{
            margin-left: 143px; 
            position: relative;
            .row{
                margin: 0;
            }
            .col-lg-4{
                padding: 0;
                width: 401px;
                margin-right: 20px;
                margin-top: 30px; /*40px*/
                display: flex;
            }
            .dm-advantages-img{
                margin-right: 45px;
                width: 60px;
                height: 60px;
            }
            
            .dm-advantages-text{
                &-title{
                    line-height: 37px;
                    font-weight: 600;
                    margin-bottom: 30px;
                }
                .dm-advantages-text-list{
                    margin: 0;
                    .dm-advantages-text-item{
                        margin-left: 17px;
                        font-size: 18px;
                        line-height: 1;
                        margin-bottom: 14px;
                        &::before {
                            content: "";
                            background-color: $text;
                            width: 3px;
                            min-width: 3px;
                            height: 3px;
                            top: 7px;
                            left: -14px;
                        }

                    }                    
                }
            }            
        }
    }
    
}