.advice{
    .container{
        padding: 79px 0;        
        width: 1440px;
        height: 577px;
        background: url(assets/img/advice-background.png) center no-repeat;  
        position: relative;
        .advice-form{
            display: flex;
            flex-direction: column;
            width: 652px;
            height: 419px;
            background: rgba(255, 255, 255, 0.7);
            margin-left:394px;
            &-title{
                font-weight: 600;
                line-height: 37px;
                padding: 81px 147px 50px;
            }
            &-item{
                margin-left: 147px;
                input[type="text"], input[type="tel"]{
                    width: 358px;
                    background: 0;
                    border: 0;
                    color: $black;
                    font-family: $font-primary;
                    font-size: 15px;
                    line-height: 18px;
                    font-weight: 400;
                    margin-bottom: 30px;  
                    border-bottom: 1px solid #000;
                    padding: 0 0 10px 0;      
                    &:focus-visible{
                        outline: none;
                    }
                }
            }   
            .btn{
                width: 250px;
                margin-left: 147px;
                padding: 19px 17px;
                &:hover{
                    padding: 19px 15px;
                }
                &:focus{
                    padding: 19px 15px;
                }
            }           
        }
        .card-block{
            position: absolute;  
            top: 98px;
            right: 425px;    
            width: 652px;
            height: 382px;
            background: #fff;
            padding: 60px;
            display: none;
            flex-direction: column;
            &-title{
                font-weight: 600;    
                margin-bottom: 40px;  
            }
            &-description{
                margin-bottom: 20px;
                p{
                    font-size: 18px;
                    line-height: 1;
                    margin-bottom: 30px;
                } 
            }
            .btn{
                margin-right: 332px; 
                width: 200px;
            }                  
        }       
    }    
}