.idm-sample-comtract{
    .container{
        padding: 100px 256px 100px 295px;
        width: 1440px;
        height: 328px;
        background: $black;
        .sample-comtract-title{
            line-height: 1;
            font-weight: 600;
            margin-bottom: 60px;
            color: $white;
            z-index: 1;
            position: relative;
            span{
                position: relative;
                &::before{
                    content: "";
                    width: 30px;
                    height: 9px;
                    background: url(assets/img/arrow-right-white.png) center no-repeat;
                    position: absolute;
                    top: 15px;
                    right: -60px;
                }
            };
        }
        .sample-comtract-block{
            color: $white;
            position: relative;
            z-index: 1;
            display: flex;
            .sample-comtract-description{
                margin-right: 152px;
            }
            .btn-secondary{
                width: 220px;
                padding: 19px 23px;
                text-decoration: none;
                &:hover {
                    text-decoration: none;
                    padding: 19px 20px;
                }
                &:focus {
                    text-decoration: none;
                    padding: 19px 20px;
                }
            }            
        }
    }    
}

