.aum-team{
    .container{
        padding: 0 0 0 100px;        
        .aum-team-title{
            font-weight: 600;
            margin: 100px 0 60px;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                transform: rotate(90deg);
                position: absolute;
                top: -20px;
                left: 185px;
            }
        }
        .aum-team-info{
            display: flex;
            .aum-team-list{
                width: 205px;
                margin-right: 116px;
                .aum-team-item{
                    margin-bottom: 20px;  /*30px*/                  
                    &::before{
                        content: "";
                        height: 0;
                    }
                    .aum-team-link{
                        font-size: 20px;
                        font-weight: 300;
                        .aum-team-subtitle{
                            display: none;
                            font-size: 15px;
                            font-weight: 300;
                            padding: 0;
                            margin-bottom: 20px;
                        }
                        &:hover{
                            text-decoration: none;
                        }
                        &.active-team{
                            font-weight: 600;
                            border-bottom: .5px solid $third-color;
                            padding-bottom: 5px; 
                            margin-bottom: 15px;
                        }
                        &.active-team  .aum-team-subtitle{
                            display: block;
                            font-size: 300;
                            margin-top: 15px;
                        }
                    }                        
                }
            }
            .aum-team-about_person{
                width: 1036px;
                display: flex;
                &.hidden {
                    display: none;
                }
                &-text{
                    margin-right: 19px;
                    &-description{
                        font-size: 18px;
                        margin-bottom: 50px;
                    }
                    .btn{
                        padding: 0;
                        font-size: 15px;
                        font-weight: 600;
                        &:hover{
                            background: none;
                            font-size: 16px;
                            padding: 0;
                        }
                        &:focus{
                            background: none;
                            font-size: 16px;
                            padding: 0;
                        }
    
                        span{
                            position: relative;
                            &::after{
                                content: "";
                                width: 30px;
                                height: 5px;
                                background: url(assets/img/arrow-right.png) center no-repeat;
                                position: absolute;
                                top: 9px;
                                right: -40px;
                                
                            }
                        }
                    }
                }
                .aum-team-img{
                    margin-top: -190px;
                    width: 710px;
                    height: 740px;
                }               
            }
            .modal{
                top: 100px;
                &-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                            .btn-close{
                                &:focus{
                                    box-shadow: none;
                                }
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 39px;
                            &-description{
                                font-size: 18px;
                                line-height: 1;
                                margin-bottom: 50px;
                            }
                            &-form{
                                &-item{
                                    input[type="text"], input[type="tel"]{
                                        width: 100%;
                                        max-width: 532px;
                                        background: 0;
                                        border: 0;
                                        color: $black;
                                        font-family: $font-primary;
                                        font-size: 15px;
                                        line-height: 1;
                                        margin-bottom: 30px;  
                                        border-bottom: 1px solid #000;
                                        padding: 0 0 10px 0;      
                                        &:focus-visible{
                                            outline: none;
                                        }
                                    }
                                    input[type="tel"]{
                                        margin-bottom: 120px;  
                                    }
                                } 
                            }                    
                            textarea{
                                width: 100%;
                                max-width: 532px;
                                font-family: $font-primary;
                                font-size: 15px;
                                line-height: 1;
                                border: 0;
                                background: none;
                                padding: 0 0 10px 0;
                                border-bottom: 1px solid #000;
                                &:focus-visible{
                                    outline: none;
                                }
                            }
                        }
                        .modal-footer{                    
                            .btn-send{
                                width: 270px;
                                margin-left: 0;
                                margin-right:267px;   
                                padding: 21px 27px;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 1;
                                &:hover{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                         
            }
            .modal#exampleModalToggleAumTeamSent {
                top: 100px;
                .modal-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 20px;
                            &-description{
                                p{
                                    font-size: 18px;
                                    line-height: 1;
                                    margin-bottom: 30px;
                                }                                    
                            }                  
                        }
                        .modal-footer{                    
                            .btn{
                                width: 200px;
                                margin-left: 0;
                                margin-right: 335px;   
                                padding: 19px 35px;
                                &:hover{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                     
            }              
            .aum-team-list-mob{
                display: none;
            }                            
        }
    }
}