.s-architectural-design{
    .container{
        padding: 0;
        display: flex;
        .architectural-design-info{
            padding: 100px 0 100px 60px;
            background: rgba(241, 236, 233, .6);
            .architectural-design-title{
                font-weight: 600;
                line-height: 24px;
                margin-bottom: 50px;
                margin-right: 403px;
                position: relative;
                &::before{
                    content: "";
                    width: 100%;
                    height: 0.5px;
                    background: $third-color;
                    position: absolute;
                    bottom: -10px;
                    left: 0;
                }
            }
            .architectural-design-description{
                font-size: 18px;
                line-height: 23px;
                margin-bottom: 50px;
                margin-right: 74px;
            }
            .btn-secondary{
                width: 200px;
                background-color:rgba(247, 244, 242, .6);
                padding: 19px 45px 18px 44px;
                &:hover {
                    background-color: #303030;
                    padding: 19px 44px 18px 43px;
                }
                &:focus {
                    background-color: $primary-color;
                    padding: 19px 44px 18px 43px;
                } 
            }
        }
    }    
}

