.dfm-info{
    .container{
        padding: 0;
        width: 1440px;
        height: 358px;
        background: url(assets/img/dfm-info-background.png) center no-repeat;
        display: flex;

        .dfm-info-title{
            font-weight: 600;
            color: $white;
            //padding: 146px 110px 0 100px;
            margin: 146px 110px 0 100px;
        }
        .dfm-info-list{
            margin-top: 100px;
            margin-right: 250px;
            .dfm-info-item{
                color: $white;
                font-size: 20px;
                line-height: 1;
                font-weight: 300; /*400*/
                margin-bottom: 28px;
                &::before{
                    content: "";
                    width: 2px;
                    min-width: 2px;
                    height: 2px;
                    top: 7px;
                    left: -17px;
                    background-color: $white;
                    border-radius: 0;
                }
            }
        }
    }
}