.s-our-services{
    .container{
        padding: 0;
        display: flex;
        .our-services-info{
            padding: 100px 70px 0 100px;
            .our-services-title{
                font-weight: 600;
                line-height: 37px;     
                margin-bottom: 60px;  
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(180deg);
                    position: absolute;
                    top: -20px;
                    left: 155px;
                }     
            }
            .our-services-description{
                font-size: 20px;
                line-height: 23px;
                margin-bottom: 20px;
            }
            .our-services-list{
                font-size: 20px;
                line-height: 23px;
                .our-services-item{
                    font-size: 18px;
                    line-height: 22px;
                    margin-bottom: 30px;  
                    margin-left: 19px;
                    &:first-child{
                       margin-top: 40px; 
                    }
                    &:last-child{
                        margin-bottom: 0px;
                    }
                    &::before{
                        content: "";
                        border-radius: 0;
                        width: 9px;
                        height: 9px;
                        background-color: $third-color;
                        left: -37px;
                        
                    }
                }
            }
        }
        .our-services-img{
            position: relative;
            &-mob{
                display: none;
            }
            &::after{
                content: "";
                width: 724px;
                height: 747px;
                background: url(assets/img/more-before-one.png) center no-repeat;
                position: absolute;
                top: 0;
                right: 0;
            }
        }
    }    
}