.dm-price{
    .container{
        padding: 0;
        display: flex;
        background: $primary-color;
        .dm-price-info{
            padding: 100px 135px 0 107px;
            .dm-price-title{
                line-height: 37px;
                font-weight: 600;
                color: #fff;
                margin-bottom: 55px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    position: absolute;
                    top: -20px;
                    left: -20px;              
                }
            }
            .table-dm-price{
                tbody{
                    tr{
                        display: flex;
                        td{
                            color: #fff;
                            padding-bottom: 30px;
                            &.first-column{
                                width: 158px;
                                font-size: 20px;
                                font-weight: 600;
                                margin-right: 70px;
                            }
                            &.second-column{
                                width: 262px;
                                font-size: 18px;
                                font-weight: 300; /*400*/
                            }
                        }
                    }
                }                    
            }
            .dm-price-description{
                color: $third-color;
                font-weight: 700;
                font-size: 20px;
                margin-bottom: 50px;
            }
            .btn-success {
                width: 300px;
                line-height: 1;
                padding: 21px 30px 20px;
                &:hover{
                    padding: 21px 26px 20px;
                }
                &:focus{
                    padding: 21px 26px 20px;
                }
            }

            .modal{
                top: 100px;
                &-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                            .btn-close{
                                &:focus{
                                    box-shadow: none;
                                }
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 39px;
                            &-description{
                                font-size: 18px;
                                line-height: 1;
                                margin-bottom: 50px;
                            }
                            &-form{
                                &-item{
                                    input[type="text"], input[type="tel"]{
                                        width: 100%;
                                        max-width: 532px;
                                        background: 0;
                                        border: 0;
                                        color: $black;
                                        font-family: $font-primary;
                                        font-size: 15px;
                                        line-height: 1;
                                        margin-bottom: 30px;  
                                        border-bottom: 1px solid #000;
                                        padding: 0 0 10px 0;      
                                        &:focus-visible{
                                            outline: none;
                                        }
                                    }
                                    input[type="tel"]{
                                        margin-bottom: 120px;  
                                    }
                                } 
                            }                    
                            textarea{
                                width: 100%;
                                max-width: 532px;
                                font-family: $font-primary;
                                font-size: 15px;
                                line-height: 1;
                                border: 0;
                                background: none;
                                padding: 0 0 10px 0;
                                border-bottom: 1px solid #000;
                                &:focus-visible{
                                    outline: none;
                                }
                            }
                        }
                        .modal-footer{                    
                            .btn-send{
                                width: 270px;
                                margin-left: 0;
                                margin-right:267px;   
                                padding: 21px 27px;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 1;
                                &:hover{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                         
            }
            .modal#exampleModalToggleDmPriceSent {
                top: 100px;
                .modal-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 20px;
                            &-description{
                                p{
                                    font-size: 18px;
                                    line-height: 1;
                                    margin-bottom: 30px;
                                    &:last-child{
                                        margin-bottom: 0;
                                    }
                                }                                    
                            }                  
                        }
                        .modal-footer{                    
                            .btn{
                                width: 200px;
                                margin-left: 0;
                                margin-right: 335px;   
                                padding: 19px 35px;
                                &:hover{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                     
            }

        }
    }
}