.markup {
    .col-lg-4.col-md-6.col-xs-12 {
        padding: 25px;
        box-shadow: 0 0 1px rgba(0, 0, 0, 0.27);

        &:hover {
            box-shadow: 0 0 1px rgb(192, 17, 17);
        }
    }

    .scroll {
        max-height: 350px;
    }

    .site-map {
        ol {
            padding-left: 40px;
            padding-bottom: 20px;

            li {
                position: relative;
                border-left: 2px solid $primary-color;
                &:hover {
                    cursor: pointer;
                    & >a {
                        text-decoration: underline;
                    }
                    &:before {
                        background-color: $secondary-color;
                    }
                }
                a {
                    line-height: 40px;
                    margin-left: 15px;
                  display: flex;
                }

                &:before {
                    content: "";
                    position: absolute;
                    left: -26px;
                    top: 15px;
                    width: 10px;
                    height: 10px;
                    background-color: $primary-color;
                    border-radius: 50%;
                   
                }

                &:after {
                    content: "";
                    position: absolute;
                    left: -2px;
                    top: 25px;
                    width: 50%;
                    height: 15px;
                    border-left: 2px solid $primary-color;
                    border-bottom: 2px solid $primary-color;
                }
            }
        }
    }

    .gradient-default {
        background: radial-gradient(rgb(206, 196, 196), transparent);
        transition: all 2s;

        &:hover {
            background: radial-gradient(red, transparent);
        }
    }
}

body {
	font-family: $font-primary;
	color: $black;
	background-color: #fff;
	overflow-x: hidden;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
    line-height: $line-height;
}

.wrap_page {
    overflow: hidden;
    margin-bottom: auto;
}

.scrolltop {
    display: none;
    width: 100%;
    margin: 0 auto;
    position: fixed;
    bottom: 20px;
    right: 10px;
    z-index: 20;
}

.muve-top {
    position: absolute;
    right: 20px;
    bottom: 20px;
    padding: 20px;
    background: $muve-top-background;
    color: $muve-top-color;
    cursor: pointer;
    transition: 0.5s;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
 
    &:before {
        content: "";
        position: absolute;
        font-weight: bold;
        font-size: 20px;
        top: 47%;
        left: 52%;
        transform: translate(-50%, -50%);
    }
}

/*ol*/
ol, ul {
    padding: 0;
    counter-reset: li;

    li {
        list-style: none;
        list-style-position: outside;
        position: relative;
        &:before {
            content: counter(li, decimal) ".";
            counter-increment: li;
            font-weight: 400;
            color: $primary-color;
            position: absolute;
            top: 0;
            left: 0
        }
    }
}
/*ul*/

ul {
    li {
        &::before {
            content: "";
            background-color: $primary-color;
            border-radius: 50%;
            width: 10px;
            min-width: 10px;
            height: 10px;
            top: 7px;
        }
    }
} 

