/*8*/
input[type="checkbox"] {
    + label[for="checkbox"] {
        font-size: 20px;
    }
}

input[type="checkbox"],
input[type="radio"] {
    display: none;

    &+label {
        color: #000;
        cursor: pointer;
        font-size: 20px;
        line-height: 1.5; 
        font-family: $font-secondary;
        text-transform: uppercase;

        &:hover {
            color: #000;
        }

        &:before {
            content: '';
            padding: 9px;
            display: inline-block;
            vertical-align: middle;
            cursor: pointer;
            margin-right: 20px;
            margin-bottom: 13px;
            width: 40px;
            height: 40px;
            background-color: #ffffff;
            border: 1px solid #CDCDCD;
        }
    }
}

input[type="checkbox"]:checked + label:before {
    background: url("assets/img/checkbox.svg") center no-repeat;
}

input[type=radio]+label:before {
    content: '';
    padding: 9px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px;
    margin-bottom: 13px;
    width: 40px;
    height: 40px;     
    border-radius: 50%;
    cursor: pointer;
    background-color: #fff;
    border: 1px solid #CDCDCD;
}

input[type=radio]:checked+label:before {
    content: '';
    background: url("assets/img/radiobutton.svg") center/18px no-repeat;
}

/*9-11*/
.btn {
    display: inline-block;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    font-size: 18px;
    font-family: $font-primary;
    font-weight: 400;
    line-height: 22px;
    border-radius: 0;
    text-decoration: none;
    &:hover {
        font-weight: 700;
        text-decoration: none;
        font-family: $font-primary;
    }
    &:focus {
        font-weight: 700;
        color: #ffffff4d;
        background-color:$third-color;
        border: 1px solid transparent;
        font-family: $font-primary;
    }
}

.btn-primary {
    color: $third-color;
    background-color: #303030;
    border: 1px solid $third-color;
    font-size: 15px;
    line-height: 1;
    padding: 18px 25px 18px 26px;
    &:hover {
        color: $third-color;
        background-color: #303030;
        border: 1px solid $third-color;
        padding: 18px 23px 18px;
        text-decoration: none;
    }
    &:focus {
        padding: 18px 23px 18px;
        color: $third-color;
        background-color: #181818;
        border: 1px solid $third-color;
        box-shadow: 0 4px 20px 0 rgba(188, 204, 105, 0.25);
    } 
    &:active:focus {
        box-shadow: 0 4px 20px 0 rgba(188, 204, 105, 0.25);
    }
}

.btn-secondary {
    color: #000;
    background-color: #fff;
    border: 1px solid #000;
    font-size: 18px;
    line-height: 21px;
    padding: 18px 34px;
    &:hover {
        color: #fff;
        background-color: #303030;
        border: 1px solid #000;
        padding: 18px 33px 18px 33px;
        box-shadow: 0 4px 10px 0 rgba(48, 48, 48, 0.25);
    }
    &:focus {
        padding: 18px 33px 18px 33px;
        color: #fff;
        background-color: $primary-color;
        box-shadow: 0 4px 10px 0 rgba(48, 48, 48, 0.25);   
    } 
    &:active:focus {
        box-shadow: 0 4px 10px 0 rgba(48, 48, 48, 0.25);   
    }
}

.btn-success {
    color: $third-color;
    background-color: $primary-color;
    border: 1px solid $third-color;
    padding: 18px 23px 18px 24px;
    &:hover {
        padding: 18px 20px 18px;
        color: $third-color;
        background-color: $primary-color;
        border: 1px solid $third-color;
        box-shadow: 0 4px 20px 0 rgba(188, 204, 105, 0.25);
    }
    &:focus {
        padding: 18px 20px 18px;
        color: $third-color;
        background-color: #000000;
        border: 1px solid $third-color;
        box-shadow: 0 4px 20px 0 rgba(188, 204, 105, 0.25);
    } 
    &:active:focus {
        box-shadow: 0 4px 20px 0 rgba(188, 204, 105, 0.25);
    }
}

.btn-danger {
    color: #000;
    background-color: #fff;
    border: none;
    font-size: 18px;
    line-height: 1;
    padding: 21px 56px 21px 57px;
    &:hover {
        color: #fff;
        background-color: #303030;
        border: none;
        padding: 21px 54px 21px 55px;
        box-shadow: 0 4px 10px 0 rgba(48, 48, 48, 0.25);
    }
    &:focus {
        padding: 21px 54px 21px 55px;
        color: #fff;
        background-color: $primary-color;
        box-shadow: 0 4px 10px 0 rgba(48, 48, 48, 0.25);   
    } 
    &:active:focus {
        padding: 22px 53px 21px 54px;
        box-shadow: 0 4px 10px 0 rgba(48, 48, 48, 0.25);   
    }
}

.btn-warning{
    color: #fff;
    background-color: #0E0E0E;
    border: none;
    padding: 19px 24px;
    &:hover {
        padding: 19px 20px 19px 21px;
        color: $third-color;
        background-color: #0E0E0E;
        border: none;
        box-shadow: 0 4px 10px 0 rgba(48, 48, 48, 0.25);
    }
    &:focus {
        padding: 19px 20px 19px 21px;
        color: $third-color;
        background-color: #0E0E0E;
        border: none;
        box-shadow: 0 4px 10px 0 rgba(48, 48, 48, 0.25);
    } 
    &:active:focus {
        box-shadow: 0 4px 10px 0 rgba(48, 48, 48, 0.25);
    }
}

.btn-equipmen{
    height: 27px;
    margin-top: 30px;
    font-weight: 300;
    color: #000;
    padding: 0 0 9px 0;
    border-bottom: 2px dotted #000;
    &:hover {
        text-decoration: none;
        font-weight:300;
        background: none;
        color: $third-color; 
        border-bottom: 2px dotted $third-color;
    }
    &:focus {
        font-weight:300;
        color: $third-color;  
        background: none;
        text-decoration: none;
        border-bottom: 2px dotted $third-color;
    }
    &:active:focus{
