        &:focus-visible {
            outline: none;
        }
    }
    [type="submit"]{
        font-family: $font-primary;
        color: #fff;
        font-weight: 700;
        font-size: 14px;
        line-height: 1;
        padding: 16px 39px;
        margin: 0 0 0 -3px;
    }
}
/*18 range-slider*/
.range-slider{
    margin: 20px 0 20px 10px;
    position: relative;
    input[type=range] {
        position: absolute;
        pointer-events: none;
        -webkit-appearance: none;
        z-index: 2;
        height: 4px;
        width: 100%;
        opacity: 0;
    }
    input[type=range]::-webkit-slider-thumb {
        pointer-events: all;
        width: 16px;
        height: 16px;
        border-radius: 0;
        border: 0 none;
        background-color: red;
        -webkit-appearance: none;
        cursor: pointer;
    }
    .slider {
        position: relative;
        z-index: 1;
        height: 1px;
        margin: 0 10px;
        > .track {
            position: absolute;
            z-index: 1;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            border-radius: 5px;
            background-color: #E0E0E0;
        }
        > .range {
            position: absolute;
            height: 3px;
            z-index: 2;
            left: 0;
            right: 0;
            top: -1px;
            bottom: 0;
            border-radius: 5px;
            background-color: #000;
        }
        > .thumb {
            position: absolute;
            cursor: pointer;
            z-index: 3;
            width: 7px;
            height: 7px;
            background-color: #000;
            border-radius: 50%;
        }
        > .thumb.left {
            left: 0;
            transform: translate(-3px, -3px);
        }
        > .thumb.right {
            right: 0;
            transform: translate(3px, -3px);
        }      
    }
    .thumb-info{
        display: flex;
        justify-content: space-between;
        .thumb{
            display: flex;
            width: 92px;
            &:nth-child(2){
                margin-right: 13px;
            }
            &-text{
                width: 16px;
                font-size: 14px;
                margin-top: 23px;
                margin-right: 12px;
                color: #000;
            }
            .price_value_min {
                white-space: nowrap;
                font-size: 14px;
                margin-top: 23px;
            }
            .price_value_max {
                white-space: nowrap;
                font-size: 14px;
                margin-top: 23px;

            }
        }    
    }      
}


/*19 modal two windows*/
.modal{
    top: 100px;
    &-dialog{
        max-width: 660px;
        .modal-content{
            height: 100%;
            padding: 60px;
            .modal-header{
                margin-bottom: 40px;
                border-bottom: none;
                .modal-title{
                    font-weight: 600;                                
                }
                .btn-close{
                    &:focus{
                        box-shadow: none;
                    }
                }
            }                        
            .modal-body{
                margin-bottom: 39px;
                &-description{
                    font-size: 18px;
                    line-height: 1;
                    margin-bottom: 50px;
                }
                &-form{
                    &-item{
                        input[type="text"], input[type="tel"]{
                            width: 100%;
                            max-width: 532px;
                            background: 0;
                            border: 0;
                            color: $black;
                            font-family: $font-primary;
                            font-size: 15px;
                            line-height: 1;
                            margin-bottom: 30px;  
                            border-bottom: 1px solid #000;
                            padding: 0 0 10px 0;      
                            &:focus-visible{
                                outline: none;
                            }
                        }
                        input[type="tel"]{
                            margin-bottom: 120px;  
                        }
                    } 
                }                    
                textarea{
                    width: 100%;
                    max-width: 532px;
                    font-family: $font-primary;
                    font-size: 15px;
                    line-height: 1;
                    border: 0;
                    background: none;
                    padding: 0 0 10px 0;
                    border-bottom: 1px solid #000;
                    &:focus-visible{
                        outline: none;
                    }
                }
            }
            .modal-footer{                    
                .btn-send{
                    margin-left: 0;
                    margin-right:267px;   
                    padding: 21px 27px;
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 1;
                    &:hover{
