.our-projects-in-instagram{
    .container{
        padding: 0;
        .opi-title{
            font-weight: 600;
            line-height: 37px;
            margin: 100px 503px 60px;
        }
        .opi-example{
            padding: 0 100px 60px 100px;
            .row{
                margin: 0;
            }
            .col-lg-4{
                padding: 0;
                margin-bottom: 20px;
                position: relative;
                &:hover .opi-example-general{
                    display: block;
                }        
            }
            &-general{
                display: none;
                img{
                    width: 400px;
                    height: 50px;
                    position: absolute;
                    bottom:0;
                    left: 0;
                    z-index: 1;
                }                
            }
        }
        .opi-links{
            display: flex;
            margin: 0 200px 100px 165px;            
            &-inst{
                font-size: 20px;
                line-height: 24px;
                margin: 19px 201px 0 201px;
                position: relative;
                &::before{
                    content: "";
                    width: 40px;
                    height: 40px;
                    background: url(assets/img/opi-icon-inst-links.png) center no-repeat;
                    position: absolute;
                    top: -5px;
                    left: -55px;
                }
            }
            .btn-warning{
                width: 270px;
                padding: 19px 29px 19px 28px;
                &:hover {
                    padding: 19px 25px 19px 24px;
                }
                &:focus {
                    padding: 19px 25px 19px 24px;
                }
            }
            .btn-secondary {
                width: 200px;
                padding: 18px 37px;
                &:hover {
                    padding: 19px 35px 18px;
                }
                &:focus {
                    padding: 19px 35px 18px;
                }
            }

        }
    }
}