.fwm-completed-projects{
    .container{
        padding: 100px 100px 0 120px;
        width: 1440px;
        height: 880px;
        background: url(assets/img/completed-projects-background.png) center no-repeat;
        position: relative;
        &::before{
            content: "";
            width: 1440px;
            height: 880px;
            position: absolute;
            top: 0;
            left: 0;
            background: rgba(23, 23, 23, .9);
        }
        .completed-projects-title{
            font-weight: 600;
            color: #fff;
            margin-bottom: 62px;
            z-index: 1;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                position: absolute;
                top: -20px;
                left: -20px;                
            }
        }
        .slider{
            z-index: 2;
            position: relative;             
            &-item{
                width: 1240px;
                height: 520px;
                &-one{
                    display: flex;
                    .completed-projects-column{
                        margin-right: 20px;
                        img{
                            margin-bottom: 20px;
                        }
                        .completed-projects-row{
                            display: flex;
                            img{
                                margin-right: 20px;
                            }
                        }
                    }
                }        
            }
            .slides-numbers {
                color: #fff;
                display: none;
                position: absolute;
                bottom: -75px;
                right: 60px;
                z-index: 3;
                .active {
                    font-size: 20px;
                    line-height: 24px;
                    margin-right: 11px;
                    position: relative;
                    &::after{
                        content: "/";
                        position: absolute;
                        width: 8px;
                        top:0;
                        right: -14px;
                    }
                }      
                .total {
                    font-size: 15px;
                    line-height: 18px;
                }
            }
            .slick-arrow {
                width: 50px;
                height: 10px;
                background: url("assets/img/icon-arrow.png") center/contain no-repeat;
                position: absolute;
                z-index: 1;
                &::before{
                    content: "";
                }
                &.slick-prev {
                    transform: rotate(180deg);
                    left: 1045px;
                    top: 576px;
                }            
                &.slick-next {
                    right: 0px;
                    top:580px;
                }
                &:hover{
                    opacity: 0.7;
                }        
            }     
        }
        .completed-projects-img{
            display: none;
        }
    } 
}