.blog-info{
    .container{
        padding: 0;
        display: flex;
        justify-content: space-between;
        .blog{
            width: 1027px;
            padding: 98px 0 0 207px;
            margin-right: 20px;
            
            &-title{
                font-weight: 600;
                margin: 0 0 60px -85px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(0deg);
                    position: absolute;
                    top: -20px;
                    left: -20px;
                }           
            }

            .slider{    
                height: 100%;
                width: 820px;
                margin-bottom: 100px;            
                position: relative;

                &-item{
                    height: 100%;
                    .blog-more{
                        margin-bottom: 100px;
                        &-title{
                            font-weight: 600;
                            margin-bottom: 26px;
                        }
                        &-other{
                            display: flex;
                            margin-bottom: 45px;
                            &-data{
                                font-weight: 300;
                                font-size: 15px;
                                margin-right: 65px;
                            }
                            &-view{
                                font-weight: 300;
                                font-size: 15px;
                                padding-left: 0px;
                                position: relative;
                                &::before{
                                    content: "";
                                    width: 21px;
                                    height: 12px;
                                    background: url(assets/img/icon-view.png) center no-repeat;
                                    position: absolute;
                                    top: 4px;
                                    left: -33px;
                                }
                            }
                        }
                        &-img{
                            width: 820px;
                            height: 500px;
                            margin-bottom: 40px;
                        }
                        &-description{
                            margin-bottom: 40px;
                            p{
                                font-size: 18px;
                                font-weight: 300;
                                padding-bottom: 10px;
                            }   
                            .more{
                                display: none;
                            }
                            .blog-more-link{
                                border: 0;
                                background: 0;
                                font-size: 18px;
                                font-weight: 300;
                                color: #000;
                                height: 27px;
                                //margin-top: 30px;                    
                                padding: 0 0 9px 0;
                                border-bottom: 2px dotted #000;
                                &:hover{
                                    opacity: .7;
                                }
                            }                                                                     
                        }                  
                    }    
                    .blog-share{
                        display: flex;
                        margin-bottom: 36px;
                        &-name{
                            margin-right: 24px;
                            color: #8C8C8C;
                            font-size: 15px;
                            font-weight: 600;
                        }
                        &-img{
                            margin-right: 10px;
                            &:hover{
                                opacity: .7;
                            }
                        }
                    }
                    .blog-teg-other{
                        margin-bottom: 58px;
                        display: flex;
                        
                        &-name{
                            color: #8C8C8C;
                            font-size: 15px;
                            font-weight: 600;
                            margin-right: 27px;                            
                        }
                        &-meaning{
                            color: #8C8C8C;
                            background: #EBEBEB; 
                            font-size: 15px;
                            padding: 8px 10px;
                            text-decoration: none;
                            &:hover{
                                text-decoration: none;
                                opacity: .7;
                            }
                            span{
                                margin-left: 10px;
                            }
                        }                        
                    }
                }
                .slides-numbers {
                    display: none;
                    position: absolute;
                    bottom: 234px;
                    right: -37px;
                    z-index: 3;
                    .active {
                        font-size: 20px;
                        line-height: 24px;
                        margin-right: 4px;
                        position: relative;
                        &::after{
                            content: "/";
                            position: absolute;
                            width: 8px;
                            top:0;
                            right: -9px;
                        }
                    }      
                    .total {
                        font-size: 15px;
                        line-height: 18px;
                    }
                }
                .slick-arrow {
                    width: 50px;
                    height: 10px;
                    background: url("assets/img/arrow-right.png") center/contain no-repeat;
                    position: absolute;
                    top: 100%;
                    z-index: 1;
                    &::before{
                        content: "";
                    }
                    &.slick-prev {
                        transform: rotate(180deg);
                        left: 755px;
                        //top: 100%;
                    }            
                    &.slick-next {
                        left: 861px;
                        margin-top: 5px;
                    }
                    &:hover{
                        opacity: 0.7;
                    }        
                }
            }  
            .blog-mob{
                display: none;
            }       
        }

        .blog-teg{
            padding: 108px 0 0 0 ;
            
            &-list {
                p{
                    font-size: 20px;
                    font-weight: 600;
                    color: #8C8C8C; 
                    margin-bottom: 36px; 
                }
                
                .blog-teg-item{
                    margin-bottom: 20px;
                    &::before{
                        content: "";
                        height: 0;
                    }
                    .blog-teg-link{
                        color: #8C8C8C;
                        background: #EBEBEB; 
                        font-size: 15px;
                        padding: 8px 10px;
                        text-decoration: none;
                        &:hover{
                            text-decoration: none;
                            opacity: .7;
                        }
                        span{
                            margin-left: 10px;
                        }
                    }
                }
            }
        }
    }

    
}
