.interior-design-more{
    .container{
        padding: 0;
        display: flex;
        .idm-info{
            padding: 100px 85px 0 100px;
            .idm-title{
                font-weight: 600;
                line-height: 37px;
                margin-bottom: 60px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(180deg);
                    position: absolute;
                    top: -20px;
                    left: 252px;
                } 
            }
            .idm-description{
                margin-bottom: 50px;
                p{
                    font-family: $font-primary;
                    font-size: 20px;
                    line-height: 23px;
                    &:first-child{
                        margin-bottom: 20px;
                    }
                }           
            }
            .btn-modal{
                width: 200px;
                padding: 19px 21px 19px 22px;
                &:hover{
                    padding: 19px 18px 19px 19px;
                }
                &:focus{
                    padding: 19px 18px 19px 19px;
                }
            }
    
            .modal{
                top: 100px;
                &-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                            .btn-close{
                                &:focus{
                                    box-shadow: none;
                                }
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 39px;
                            &-description{
                                font-size: 18px;
                                line-height: 1;
                                margin-bottom: 50px;
                            }
                            &-form{
                                &-item{
                                    input[type="text"], input[type="tel"]{
                                        width: 100%;
                                        max-width: 532px;
                                        background: 0;
                                        border: 0;
                                        color: $black;
                                        font-family: $font-primary;
                                        font-size: 15px;
                                        line-height: 1;
                                        margin-bottom: 30px;  
                                        border-bottom: 1px solid #000;
                                        padding: 0 0 10px 0;      
                                        &:focus-visible{
                                            outline: none;
                                        }
                                    }
                                    input[type="tel"]{
                                        margin-bottom: 120px;  
                                    }
                                } 
                            }                    
                            textarea{
                                width: 100%;
                                max-width: 532px;
                                font-family: $font-primary;
                                font-size: 15px;
                                line-height: 1;
                                border: 0;
                                background: none;
                                padding: 0 0 10px 0;
                                border-bottom: 1px solid #000;
                                &:focus-visible{
                                    outline: none;
                                }
                            }
                        }
                        .modal-footer{                    
                            .btn-send{
                                width: 270px;
                                margin-left: 0;
                                margin-right:267px;   
                                padding: 21px 27px;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 1;
                                &:hover{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                         
            }
            .modal#exampleModalToggleIdmSent {
                top: 100px;
                .modal-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 20px;
                            &-description{
                                p{
                                    font-size: 18px;
                                    line-height: 1;
                                    margin-bottom: 30px;
                                }                                    
                            }                  
                        }
                        .modal-footer{                    
                            .btn{
                                width: 200px;
                                margin-left: 0;
                                margin-right: 335px;   
                                padding: 19px 35px;
                                &:hover{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                     
            }
    
        }
        .idm-photo{
            position: relative;
            &::before{
                content: "";
                width: 710px;
                height: 748px;
                background: url(assets/img/more-before-idm.png) center no-repeat;
                position: absolute;
                top: 0;
                right: 0;
            }
        }
    }
    
}