.portfolio{
    .container{
        padding: 0;
        width: 1440px;
        height: 2990px;
        background: url(assets/img/portfolio-more-background.png) center no-repeat;
        position: relative;
        z-index: 0;
        &::before{
            content: "";
            width: 1440px;
            height: 2990px;
            background: rgba(23, 23, 23, .9);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 0;
        }
        .pbw-info{
            display: flex;
            .pbw-title{
                font-weight: 600;
                line-height: 37px;
                color: $white;        
                padding: 87px 195px 60px 120px;
                margin-bottom: 0;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    position: absolute;
                    top: 67px;
                    left: 100px;                
                }
            }
            .pbw-nav{
                &-list{
                    display: flex;
                    margin-top: 92px;
                    margin-bottom: 0;
                    .pbw-nav-item{
                        margin-right: 80px;
                        &::before{
                            content: "";
                            height: 0;
                        }
                        .pbw-nav-link{
                            color: $white;
                            font-size: 20px;
                            line-height: 24px;
                            font-family: $font-primary;
                            &:hover{
                                text-decoration: none;
                                font-family: $font-primary;
                            }
                            &.active-pbw{
                                font-weight: 700;
                                padding-bottom: 5px;
                                box-shadow: inset 0 -.3px 0 0 $third-color;
                                font-family: $font-primary;
                            }    
                        }
                    }
                }
            }
        }
        .hidden{
            display: none;
        }
        .pbw-apartments{
            position: relative;
            z-index: 1;
            .slider{                
                position: relative;                
                &-item{ 
                    .row{
                        margin: 0;
                        justify-content: space-between;
                        .portfolio-apartments-link{
                            padding: 0;
                            width: 710px;
                            position: relative;
                            .col-lg-6{
                                padding: 0;
                                margin-bottom: 20px;
                                cursor: pointer;                                
                            }
                            &:hover::before{
                                content: "";
                                width: 710px;
                                height: 510px;
                                background: rgba(0, 0, 0, 0.5);
                                position: absolute;
                                top: 0px;
                                left: 0px;
                                z-index: 1;
                            }
                            &:hover .portfolio-apartments-name{
                                display: block;
                            }
                            .portfolio-apartments-name{
                                display: none;
                                position: absolute;
                                top: 245px;
                                left:262px;
                                color: $white;
                                font-size: 20px;
                                line-height: 24px;
                                font-weight: 600;
                                z-index: 1;
                            }
                        }
                    }           
                }
                .slides-numbers {
                    color: #fff;
                    display: none;
                    position: absolute;
                    bottom: -52px;
                    right: 160px;
                    z-index: 3;
                    .active {
                        font-size: 20px;
                        line-height: 24px;
                        margin-right: 4px;
                        position: relative;
                        &::after{
                            content: "/";
                            position: absolute;
                            width: 8px;
                            top:0;
                            right: -9px;
                        }
                    }      
                    .total {
                        font-size: 15px;
                        line-height: 18px;
                    }
                }
                .slick-arrow {
                    width: 50px;
                    height: 10px;
                    background: url("assets/img/icon-arrow.png") center/contain no-repeat;
                    position: absolute;
                    z-index: 1;
                    &::before{
                        content: "";
                    }
                    &.slick-prev {
                        transform: rotate(180deg);
                        left: 1172px;
                        top: 2688px;
                    }            
                    &.slick-next {
                        left: 1290px;
                        top: 2691px;
                    }
                    &:hover{
                        opacity: 0.7;
                    }        
                }
        
            }    
            &-mob{
                display: none;
            }  
        }  
    }
}


