.reviews-more{
    .container{
        padding: 100px;
        width: 1440px;
        height: 2819px;
        background: url(assets/img/reviews-more-background.png) center no-repeat;
        .reviews-more-title{
            font-weight: 600;
            color: $white;
            margin-bottom: 60px;
            margin-left: 20px;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                transform: rotate(0deg);
                position: absolute;
                top: -20px;
                left: -20px;
            }
        }
        .slider{                
            position: relative;
            
            &-item{
                .reviews-more-block{
                    background: $white;            
                    display: flex;
                    margin-bottom: 100px;                 
                    &-info{
                        padding: 90px 105px 100px 100px;
                        &-top{
                            display: flex;
                            &-icon{
                                width: 40px;
                                height: 40px;
                            }
                            &-name{
                                font-size: 20px; 
                                font-weight: 700; /*600*/
                                margin: 10px 0 50px 9px;
        
                            }
                        }                    
                        &-description{
                            p{
                                margin-bottom: 10px;
                                font-size: 16px; /*18px*/
                            }
                        }
                    }                    
                }
            }
            .slides-numbers {
                color: #fff;
                display: none;
                position: absolute;
                bottom: 23px;
                right: 60px;
                z-index: 3;
                .active {
                    font-size: 20px;
                    line-height: 24px;
                    margin-right: 4px;
                    position: relative;
                    &::after{
                        content: "/";
                        position: absolute;
                        width: 8px;
                        top:0;
                        right: -9px;
                    }
                }      
                .total {
                    font-size: 15px;
                    line-height: 18px;
                }
            }
            .slick-arrow {
                width: 50px;
                height: 10px;
                background: url("assets/img/icon-arrow.png") center/contain no-repeat;
                position: absolute;
                z-index: 1;
                &::before{
                    content: "";
                }
                &.slick-prev {
                    transform: rotate(180deg);
                    left: 1070px;
                    top: 2483px;
                }            
                &.slick-next {
                    left: 1190px;
                    top: 2487px;
                }
                &:hover{
                    opacity: 0.7;
                }        
            }
        }  
        .reviews-more-block-mob{
            display: none;
        }          
    }
}