.aum-contacts{
    .container{
        padding: 0;
        display: flex;
        .aum-contacts-address{
            width: 720px;
            padding: 100px 65px 0 120px;
            &-title{
                font-weight: 600;
                margin-bottom: 60px;
                position: relative;
                &::before{
                    content: "";
                    width: 72px;
                    height: 72px;
                    background: url(assets/img/union-icon.png) center no-repeat;
                    transform: rotate(00deg);
                    position: absolute;
                    top: -20px;
                    left: -20px;
                }
            }
            &-subtitle{
                font-weight: 600;
                margin-bottom: 40px;
                &:nth-child(4){
                    margin: 20px 0 20px;
                }
            }
            .aum-contacts-table{
                tbody{
                    font-size: 18px;
                    line-height: 1;
                    font-weight: 400;
                    tr{
                        display: flex;
                        td{
                            padding-bottom: 20px; /*30px*/
                            &.column-left{
                                font-weight: 600;
                                margin-right: 19px;
                                width: 100%;
                                max-width: 145px;
                            }
                            &.column-right{
                                p{
                                    padding: 0;
                                }
                            }    
                        }
                    }
                }  
            }
            &-more{
                font-size: 15px;
                line-height: 1;
                b{
                    font-weight: 600;
                    font-size: 15px;
                }
            }
        }

        .aum-contacts-map{
            position: relative;
            iframe{
                width: 720px;
                height: 684px;
            }
            .map-more{
                position: absolute;
                top: 54px;
                right: 100px;
                padding: 30px;
                width: 313px;
                height: 160px;
                background: #0E0E0E;
                color: $white;
                &-name{
                    font-size: 13px;
                    margin-bottom:10px;
                    b{
                       font-size: 15px; 
                       font-weight: 600;
                       color: $white;
                    }                                        
                }
                &-address{
                    font-size: 13px;
                    margin-left: 23px;
                    position: relative;
                    &::before{
                        content: "";
                        width: 15px;
                        height: 15px;
                        background: url(assets/img/icon-address.png) center no-repeat;
                        position: absolute;
                        top: 0px;
                        left: -22px;
                    }
                }
                &-tel{
                    position: relative;
                    &::before{
                        content: "";
                        width: 15px;
                        height: 15px;
                        background: url(assets/img/icon-tel.png) center no-repeat;
                        position: absolute;
                        top: 12px;
                        left: 2px;
                    }
                    p{
                        color: $white;
                        font-size: 13px;
                        padding: 10px 0 0;
                        margin: 0;
                        margin-left: 23px;
                    }
                    
                }

            }
            
        }
    }
}