.ask-question{
    .container{
        padding: 0;
        height: 704px;
        display: flex;
        .ask-question-info{
            color: $white;
            padding: 187px 170px;
            position: relative;
            &::after{
                content: "";
                width: 625px;
                height: 704px;
                background: url(assets/img/ask-question-after.png) center no-repeat;
                position: absolute;
                top: 0;
                left: 0;
                z-index: -1;
            }
            &-title{
                font-weight: 600;
                line-height: 24px;
                color: $white;
                margin-bottom: 20px;
            }
            &-founder{
                font-weight: 300;
                font-size: 15px;
                line-height: 18px;
                margin-bottom: 30px;
            }
            &-descriotion{
                font-weight: 400;
                font-size: 18px;
                line-height: 21px;
                margin-bottom: 50px;
            }
            .btn-modal{
                width: 250px;
            }
            .modal{
                top: 100px;
                &-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                            .btn-close{
                                &:focus{
                                    box-shadow: none;
                                }
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 39px;
                            &-description{
                                font-size: 18px;
                                line-height: 1;
                                margin-bottom: 50px;
                                color: #000;
                            }
                            &-form{
                                &-item{
                                    input[type="text"], input[type="tel"]{
                                        width: 100%;
                                        max-width: 532px;
                                        background: 0;
                                        border: 0;
                                        color: $black;
                                        font-family: $font-primary;
                                        font-size: 15px;
                                        line-height: 1;
                                        margin-bottom: 30px;  
                                        border-bottom: 1px solid #000;
                                        padding: 0 0 10px 0;      
                                        &:focus-visible{
                                            outline: none;
                                        }
                                    }
                                    input[type="tel"]{
                                        margin-bottom: 120px;  
                                    }
                                } 
                            }                    
                            textarea{
                                width: 100%;
                                max-width: 532px;
                                font-family: $font-primary;
                                font-size: 15px;
                                line-height: 1;
                                border: 0;
                                background: none;
                                padding: 0 0 10px 0;
                                border-bottom: 1px solid #000;
                                &:focus-visible{
                                    outline: none;
                                }
                            }
                        }
                        .modal-footer{                    
                            .btn-send{
                                width: 270px;
                                margin-left: 0;
                                margin-right:267px;   
                                padding: 21px 27px;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 1;
                                &:hover{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 21px 25px 21px 24px;                                    
                                }
                            }
                        }  
                    }
                }                                
            }
            .modal#exampleModalToggleAskSent {
                top: 100px;
                .modal-dialog{
                    max-width: 660px;
                    .modal-content{
                        height: 100%;
                        padding: 60px;
                        .modal-header{
                            margin-bottom: 40px;
                            border-bottom: none;
                            .modal-title{
                                font-weight: 600;                                
                            }
                        }                        
                        .modal-body{
                            margin-bottom: 20px;
                            &-description{
                                p{
                                    font-size: 18px;
                                    line-height: 1;
                                    margin-bottom: 30px;
                                }                                    
                            }                  
                        }
                        .modal-footer{                    
                            .btn{
                                width: 200px;
                                margin-left: 0;
                                margin-right: 335px;   
                                padding: 19px 35px;
                                &:hover{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                                &:focus{
                                    font-weight: 700;
                                    padding: 19px 32px 19px 33px;                                    
                                }
                            }
                        }                        
                        
                    }
                }                     
            }
        }
        .ask-question-img{
            height: 704px;
        }
        .ask-question-img-mob{
            display: none;
        }        
    }    
}