.rcSmonly-other{
    .container{
        padding: 100px 100px 0 0;
        height: 1213px;
        background: $black;
        .rcSmonly-other-title{
            color: $white;
            font-weight: 600;
            margin-bottom: 60px;
            margin-left: 120px;
            position: relative;               
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                transform: rotate(00deg);
                position: absolute;
                top: -20px;
                left: -20px;
            }
        }
        .rcSmonly-other-similar_projects{
            margin-bottom: 127px;
            margin-left: 100px;
            .slider{
                width: 100%;
                z-index: 2;
                position: relative;              
                &-item{
                    display: flex;     
                    justify-content: space-between;
                    .rcSmonly-other-sp-link{
                        padding: 0;
                        width: 295px;
                        position: relative;
                        margin-bottom: 20px;
                        &:hover::before{
                            content: "";
                            width: 295px;
                            height: 295px;
                            background: rgba(0, 0, 0, 0.5);
                            position: absolute;
                            top: 0px;
                            left: 0px;
                            z-index: 1;
                        }
                        &:hover .rcSmonly-other-sp-name{
                            display: block;
                        }
                        .rcSmonly-other-sp-name{
                            display: none;
                            position: absolute;
                            top: 138px;
                            left: 55px;
                            color: $white;
                            font-size: 20px;
                            line-height: 24px;
                            font-weight: 600;
                            z-index: 1;
                        }
                    }
                }    
                .slick-arrow {
                    width: 50px;
                    height: 10px;
                    background: url("assets/img/icon-arrow.png") center/contain no-repeat;
                    position: absolute;
                    z-index: 1;
                    &::before{
                        content: "";
                    }
                    &.slick-prev {
                        transform: rotate(180deg);
                        left: 1053px;
                        top: 353px;
                    }            
                    &.slick-next {
                        top: 357px;
                        right: 0px;
                    }
                    &:hover{
                        opacity: 0.7;
                    }        
                } 
                .slides-numbers {                    
                    color: #fff;
                    position: absolute;
                    bottom:-54px;
                    right: 60px;
                    z-index: 3;
                    .active {
                        font-size: 20px;
                        line-height: 24px;
                        margin-right: 4px;
                        position: relative;
                        &::after{
                            content: "/";
                            position: absolute;
                            width: 8px;
                            top:0;
                            right: -9px;
                        }
                    }      
                    .total {
                        font-size: 15px;
                        line-height: 18px;
                    }
                }            
            }          
        }
        .rcSmonly-other-to_reflection{
            margin-left: 100px;
            display: flex;
            justify-content: space-between;
            .rcSmonly-other-tp-link{
                padding: 0;
                width: 610px;
                height: 310px;
                position: relative;
                &:hover::before{
                    content: "";
                    width: 610px;
                    height: 350px;
                    background: rgba(0, 0, 0, 0.5);
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    z-index: 1;
                }
                &:hover .rcSmonly-other-tp-name{
                    display: block;
                }
                .rcSmonly-other-tp-name{
                    display: none;
                    position: absolute;
                    top: 166px;
                    left: 254px;
                    color: $white;
                    font-size: 20px;
                    line-height: 24px;
                    font-weight: 600;
                    z-index: 1;
                }
            }   
        }
    }
}

