.rmm-advantages{
    .container{
        padding: 100px;
        position: relative;
        height: 443px;
        width: 1440px;
        background: url(assets/img/rmm-advantages-background.png) center no-repeat;
        &::before{
            content: "";
            width: 1440px;
            height: 443px;
            background: rgba(255, 255, 255, 0.5);
            position: absolute;
            top: 0px;
            left: 0px;
        }
        .rmm-advantages-title{
            line-height: 37px;
            font-weight: 600;
            margin-right:480px;
            position: relative;
            &::before{
                content: "";
                width: 72px;
                height: 72px;
                background: url(assets/img/union-icon.png) center no-repeat;
                position: absolute;
                top: -20px;
                left: -20px;              
            }
        }
        .rmm-advantages-info{
            position: relative;
            .row{
                margin: 0;
            }
            .col-lg-4{
                padding: 0;
                width: 400px;
                margin-right: 20px;
                margin-top: 60px;
                display: flex;
                &:last-child{
                    margin-right: 0;
                }
            }
            .rmm-advantages-img{
                margin-right: 45px;
                width: 60px;
                height: 60px;
            }
            .rmm-advantages-text{
                &-title{
                    line-height: 37px;
                    font-weight: 600;
                    margin-bottom: 30px;
                }
                &-description{
                    font-size: 15px;
                    line-height: 18px;
                    font-weight: 400;
                }
            }
        }
    }    
}